<template>
  <div class="ds-designer ds-seat-container ds-seat-view">
    <div class="designer-head">
      <top-tools
        v-bind.sync="postData"
        :relative-list.sync="relativeList"
        :seat-obj="seatObj"
        :relative-obj="relativeObj"
        @save="save"
        @clear="clear"
      ></top-tools>
    </div>
    <div class="designer-content">
      <div class="seat-designer-center">
        <left-tree 
          :relative-list.sync="relativeList"
          :person-obj.sync="personObj"
          :seat-obj="seatObj"
          :relative-obj="relativeObj"
        ></left-tree>
        <seat-canvas>
          <template v-slot:seat>
            <seat-item
              v-bind.sync="postData"
              :relative-list.sync="relativeList"
              :chosen-items.sync="chosenItems"
              :key-stack="keyStack"
              :seat-obj="seatObj"
              :person-obj="personObj"
              :relative-obj="relativeObj"
            >
            </seat-item>
          </template>
          <template v-slot:seatPop>
            <seat-pop
              :relative-list.sync="relativeList"
              :chosen-items.sync="chosenItems"
              :seat-obj="seatObj"
              :relative-obj="relativeObj"
            >
            </seat-pop>
          </template>
        </seat-canvas>
      </div>
    </div>
  </div>
</template>

<script>
  import topTools from "_platform/designer/seat/seatView/topTools";
  import leftTree from "_platform/designer/seat/seatView/leftTree";
  import canvas from "_platform/designer/seat/seatView/canvas.vue";
  import seatItem from "_platform/designer/seat/seatView/seatItem";
  import seatPop from "_platform/designer/seat/seatView/seatPop"
  
  import {changePanelSizeForBgImage, sysBindShortKey} from "_platform/designer/seat/func";
  import eventBus from "_platform/designer/seat/eventBus";
  import {eventBusEvent} from "_platform/designer/seat/constant";
  
  export default {
    name: "DsfSeatView",
    data() {
      return {
        keyStack: [],
        
        //下面这些为提供给子组件的props
        postData: {
          _id: "",
          
          //用于top-tools组件的
          backgroundImage: {},
          canvasSet: {},
          hasBg: false,
          
          //用于seat-item组件的
          items: [],
          groups: [],
        },
        
        //用于left-tree组件的
        leftList: [],
        personObj: {},    //所有的用户k/v
        
        
        //所有的入座情况
        relativeList: [],
        
        //多个选中的座位
        chosenItems: [],
        
        //包含所有座位的对象，以座位号为key(只是座位信息，无排座信息)
        seatObj: null
      }
    },
    computed: {
      relativeObj() {
        const obj = {};
        for (let i = 0; i < this.relativeList.length; i++) {
          let rel = this.relativeList[i];
          obj[rel.seatNum] = rel;
        }
        return obj
      },
    },
    watch: {
      "postData.backgroundImage"() {
        changePanelSizeForBgImage.call(this);
      }
    },
    components: {
      topTools,
      leftTree,
      seatCanvas: canvas,
      seatItem,
      seatPop
    },
    mounted() {
      this.getSeatInfo();
      this.bindShortKey();
    },
    methods: {
      getSeatInfo() {
        const id = this.$route.query.id;
        if (!id) return;
        const data = JSON.parse(localStorage.getItem("seat_" + id));
        if (!data) return;
        if (_.isEmpty(data.canvasSet)) {
          data.canvasSet = {
            width: undefined,
            height: undefined
          }
        }
        this.postData = data;
        
        //生成seatObj 对象
        const seatObj = {};
        data.items.forEach(a => {
          seatObj[a.seatNum] = a
        })
        data.groups.forEach(a => {
          a.member.forEach(b => {
            seatObj[b.seatNum] = b
          })
        })
        this.seatObj = seatObj
        
        const relId = this.$route.query.relId;
        if (!relId) return;
        const relativeList = JSON.parse(localStorage.getItem("relation_" + relId));
        if (!relativeList) return;
        this.relativeList = relativeList;
      },
      bindShortKey() {
        const stopList = ["ctrl", "ctrl-d", "ctrl-s"];
        sysBindShortKey(this.keyStack, stopList);
        
        $(".seat-designer-page").on("click", function () {
          eventBus.dispatch(eventBusEvent.BODY_EMPTY_CLICK);
        })
      },
      save() {
        const relId = this.$route.query.relId || dsf.uuid();
        localStorage.setItem("relation_" + relId, JSON.stringify(this.relativeList));
        const originQuery = this.$route.query;
        const _query = Object.assign({}, originQuery, {relId: relId})
        this.$router.replace({path: this.$route.path, query: _query})
      },
      clear() {
        this.relativeList = [];
      },
    }
  }
</script>