<template>
  <el-container>
    <el-header style="padding: 10px 0px; text-align: right">
      <DsfButton @click="add">新增页签</DsfButton>
    </el-header>
    <el-main style="padding: 0px">
      <el-table
        ref="table"
        class="ds-table"
        :border="true"
        :data="tableData"
        :row-class-name="tableRowClassName"
      >
        <el-table-column
          label="显示名称"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.title"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          label="标识"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.name"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          label="链接"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.url"></el-input>
          </template>
        </el-table-column>
        <el-table-column
          label="搜索框"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-checkbox v-model="scope.row.isSearch"></el-checkbox>
          </template>
        </el-table-column>
        <el-table-column
          label="默认选中"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-checkbox
              v-model="scope.row.isCheckd"
              :disabled="scope.row.isCheckd"
              @change="(v) => isCheckdChange(v, scope.$index)"
            ></el-checkbox>
          </template>
        </el-table-column>
        <el-table-column
          label="隐藏"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-checkbox v-model="scope.row.isHide"></el-checkbox>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          :header-align="'center'"
          :align="'center'"
          :width="120"
        >
          <template v-slot:default="scope">
            <DsfButton
              :size="'small'"
              title="插入"
              icon="hao"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px"
              @click="insert(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="删除"
              icon="guanbi2"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px"
              @click="deleted(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="上移"
              icon="shang"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px"
              @click="up(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="下移"
              icon="xia"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px"
              @click="down(scope)"
            ></DsfButton>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";
export default {
  name: "ScheduleTabEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      names: [],
    };
  },
  created() {
    this.tableData = this.currentData || [this.newTab()];
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      let res = "";
      if (row.name) {
        this.names[rowIndex] = "";
        let i = this.names.indexOf(row.name);
        if (i != -1 && i < rowIndex) {
          res = "warning-row";
        }
        this.names[rowIndex] = row.name;
      }
      return res;
    },
    // 生成一行新数据
    newTab() {
      return {
        title: "Tab" + (this.tableData.length + 1),
        name: null,
        url: "",
        isHide: false,
        isSearch: true,
        isCheckd: !this.tableData.length,
        controls: [],
        error: false,
      };
    },
    // 新增
    add() {
      this.tableData.push(this.newTab());
    },
    // 插入
    insert(scope) {
      this.tableData.splice(scope.$index + 1, 0, this.newTab());
    },
    // 删除
    deleted(scope) {
      dsf.array.remove(this.tableData, scope.row);
    },
    // 上移
    up(scope) {
      if (scope.$index > 0) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex - 1, 0, curr);
      }
    },
    // 下移
    down(scope) {
      if (this.tableData.length - 1 > scope.$index) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex + 1, 0, curr);
      }
    },
    // 验证
    validate() {
      for (let i in this.tableData) {
        this.tableData[i]["name"] = this.tableData[i]["name"] || dsf.uuid();
        let index = this.names.indexOf(this.tableData[i]["name"]);
        if (index != -1 && index < i) {
          dsf.layer.message("请解决重复标识", false);
          return false;
        }
      }
      return true;
    },
    // 确认后传出 数据
    yes() {
      return this.tableData;
    },
    // 是否默认选中 事件
    isCheckdChange(value, index) {
      if (value) {
        this.tableData.map((row, i) => {
          if (i !== index && row.isCheckd) {
            this.tableData[i].isCheckd = false;
          }
        });
      }
    },
  },
};
</script>
