<template>
  <div class="designer-left">
    <div class="designer-left-tools">
      <div class="title">
        <span>{{ title }}</span>
        <dsf-icon @click="clickSort" name="paixu"></dsf-icon>
      </div>
      <el-tree
        ref="leftTree"
        :data="list"
        :props="leafProps"
        :lazy="lazy"
        :load="lazyLoad"
        :default-expand-all="false"
        :expand-on-click-node="false"
        :check-on-click-node="true"
        :show-checkbox="openMultipleChoose"
        @node-expand="nodeExpand"
        :filter-node-method="filterNode"
        :node-key="personIdAlias"
      >
        <div class="my-tree-node" slot-scope="{node, data}" :class="{'draggable': data.draggable}" :data-id="data[leafProps.id]">
          {{ data[leafProps.label] }}
        </div>
      </el-tree>
    </div>

  </div>
</template>

<script>
  import {jqui} from "_platform/pc/common/importJqueryUI";
  import {mapState} from "vuex";
  import SeatConfig, {leftTree, viewSeatLayout} from "_platform/public/seat_config";
  import eventBus from "_platform/designer/seat/eventBus";
  import {eventBusEvent} from "_platform/designer/seat/constant";
  import {newRelative} from "_platform/designer/seat/func";
  
  export default {
  name: "LeftTree",
  props: {
    personObj: {
      type: Object,
      default() {
        return {};
      }
    },
    relativeList:{
      type: Array,
      default(){
        return []
      }
    },
    seatObj: {
      type: Object,
      default() {
        return null
      }
    },
    relativeObj: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  data() {
    return {
      title: "",
      leafProps: {},
      lazy: false,
      isLeaf: "isLeaf",
      openMultipleChoose: false, //默认不开启多选
      list: [],
    };
  },
  watch:{
    relativeList:{
      handler(){
        this.$refs.leftTree.setCheckedNodes([]); //清空已选择的节点
        this.$refs.leftTree.filter()
      },
      deep: true
    }
  },
  computed: {
    ...mapState("seatDesign", ["panelSize", "compelPanelSize"]),
    personIdAlias(){
      return this.leafProps.id
    },
    childrenAlias(){
      return this.leafProps.children
    },
    seatedPerson(){
      const obj = {};
      for (let i=0; i<this.relativeList.length; i++){
        let a = this.relativeList[i];
        if (!a.person) continue;
        let personId = a['person'][this.personIdAlias]
        obj[personId] = a.person
      }
      return obj
    }
  },
  created() {
    this.leftTreeConfig = new SeatConfig(leftTree);
    Object.assign(this, this.leftTreeConfig); //将config填写进data
    this.layoutConfig = new SeatConfig(viewSeatLayout);
  },
  mounted() {
    this.getLeafs();
    this.addPersonRevertListener();
  },
  methods: {
    filterNode(val, data){
      data['sys_seated'] = !!this.seatedPerson[data[this.personIdAlias]]  //加入已入座标识
      return !this.seatedPerson[data[this.personIdAlias]]
    },
    clickSort() {},
    getLeafs() {
      let url = dsf.express.replace(this.leftTreeConfig.getLeafsUrl);
      this.$http.get(url).done((res) => {
        let result;
        if (_.isFunction(this.leftTreeConfig.dealRespond)) {
          result = this.leftTreeConfig.dealRespond(res.data);
        } else {
          result = res.data;
        }
        
        const personObj = {};
        const orderPerson = [];
        //递归函数
        const recurData = list => {
          for (let i=0; i<list.length; i++){
            let item = list[i];
            if (_.isFunction(this.leftTreeConfig.isDragable)) {
              item.draggable = this.leftTreeConfig.isDragable(item);
            }
            else {
              item.draggable = true
            }
            if (_.isFunction(this.leftTreeConfig.isUser)) {
              item.realUser = this.leftTreeConfig.isUser(item);
              if (item.realUser) {
                orderPerson.push(item);
                personObj[item[this.personIdAlias]] = item
              }
            }
            else {
              item.realUser = true;
              orderPerson.push(item);
              personObj[item[this.personIdAlias]] = item
            }
            if (_.isArray(item[this.childrenAlias]) && item[this.childrenAlias].length > 0) {
              recurData(item[this.childrenAlias]);
            }
          }
          return list;
        };
        this.list = recurData(result);
        
        this.orderPerson = orderPerson;
        this.$emit("update:personObj", personObj)
  
        //添加draggable交互
        this.$nextTick(() => {
          this.$refs.leftTree.filter()
          this.createInteraction();
          this.listenAutoSeat();
        });
        
      });
    },
    lazyLoad(node, resolve) {
      const func = this.leftTreeConfig.lazyLoadFunc;
      if (!_.isFunction(func)) return;
      func(node, resolve);
    },
    createInteraction() {
      const self = this;
      jqui.then(() => {
        $(".my-tree-node.draggable").draggable({
          addClasses: false,
          start(evt) {
            console.log("ss")
          },
          helper() {
            if (!self.openMultipleChoose) {
              let txt = $(this).text();
              let personId = $(this).data("id");
              return $(`<div class="drag-item" data-id=${personId}>${txt}</div>`);
            } else {
              //nodes只包含叶子节点
              let nodes = self.$refs["leftTree"].getCheckedNodes(true);
              
              nodes = nodes.filter(a => a.realUser && !a["sys_seated"]);
              if (nodes?.length > 0) {
                let str = `<div class="drag-item" multiple-choose="true">`;
                nodes.forEach((a) => {
                  str += `<span data-id=${a[self.leafProps.id]}>${a[self.leafProps.label]}</span>`;
                });
                str += `</div>`;
                return $(str);
              }
              return $(`<div class="drag-item" empty-person="true">没有可以排座的用户</div>`)
            }
          },
          cursor: "default",
          cursorAt: {
            top: -5,
            left: -5
          },
          stop(evt, ui) {
      
          }
        });
      });
    },
    nodeExpand() {
      this.$nextTick(() => {
        this.createInteraction();
      });
    },
    addPersonRevertListener() {
      eventBus.on(eventBusEvent.PERSON_REVERT_TREE, (personId) => {
        $(".my-tree-node.draggable").draggable("option", "revert", true);
      });
    },
  
    listenAutoSeat(){
      eventBus.on(eventBusEvent.AUTO_SEAT, ()=>{
        const excludePersonId = [];
        const temp = []   //temp里面保存的是有人并且被锁定了的入座信息
        this.relativeList.forEach(a => {
          if (a.currentState.couldSeat === 2 && a.person) {
            temp.push(a)
            excludePersonId.push(a.person[this.personIdAlias])
          }
        })
        
        //去除被锁定的person
        this.orderPerson = this.orderPerson.filter(a => !_.includes(excludePersonId, a[this.personIdAlias]))
        
        const temp2 = []
        let i=1, k=0;
        while ( k < this.orderPerson.length){
          let person = this.orderPerson[k];
          let seat = this.seatObj[i];
          let rel = this.relativeObj[i];
          if (!seat) break;
          if (rel?.currentState?.couldSeat === 2){
            i++;
            continue;
          }
          if (_.isFunction(this.layoutConfig.isSeatAble)) {
            let isSeatAble = this.layoutConfig.isSeatAble(seat, person)
            if (!isSeatAble) {
              i++
              continue;
            }
          }
          temp2.push(newRelative(seat, person))
          i++;
          k++;
        }
        
        this.$emit("update:relativeList", _.concat(temp, temp2))
      })
    },
    
  }
};
</script>