import { Node } from "tiptap/dist/tiptap.common"
import { wrappingInputRule, toggleWrap } from "tiptap-commands/dist/commands.common"
export default class Paragraph extends Node {

  get name() {
    return 'paragraph'
  } 

  get defaultOptions() {
    return {
      style: null,
      class: null,
    }
  }  

  get schema() {
    return {
      attrs: {
        style: {
          default: null,
        },
        class: {
          default: null,
        }
      },      
      content: 'inline*',
      draggable: false,
      group: 'block',
      parseDOM: [
        {
          tag: 'div',
          getAttrs: dom => {
            return {
              style: dom.getAttribute('style'),
              class: dom.getAttribute('class'),            
            }
          },
        },
      0 ],
      toDOM: node => {
          return ['div', {      
            ...node.attrs,
          }, 0]
      },
    };
  }

  commands({ type }) {
    return (attrs) => toggleWrap(type, attrs);
  }

  inputRules({ type }) {
    return [
      wrappingInputRule(/^\s*>\s$/, type),
    ]
  }  

}
