<template>
  <el-tabs v-model="activeName" style="height:100%" class="design-development-edit" @tab-click="tabsCliekHandler">
    <el-tab-pane name="attrs">
      <div slot="label" style="width:100px;text-align:center">属性/事件</div>
      <MonacoEditor ref="attrsEditor" :language="'json'" @mounted="editorLoaded('attrs')"></MonacoEditor>
    </el-tab-pane>
    <el-tab-pane name="slots">
      <div slot="label" style="width:100px;text-align:center">插槽</div>
      <MonacoEditor ref="slotsEditor" :language="'html'" @mounted="editorLoaded('slots')"></MonacoEditor>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";

export default {
  name: 'DesDevelopment',
  mixins: [propertyDialogSetting],
  data() {
    return {
      activeName: "attrs",
      data: {}
    }
  },
  created() {
    this.data = dsf.mix(true, {}, this.currentData);
  },
  mounted() {

  },
  methods: {
    yes() {
      let obj = {
        attrs: {},
        slots: {}
      }
      for (let k in obj) {
        let editor = this.$refs[k + "Editor"];
        if (!editor) {
          continue;
        }
        if (k == 'attrs') {
          obj[k] = JSON.parse(editor.getContent() || "{}");
        }
        else {
          obj[k] = editor.getContent() || "";
        }
      }
      return obj;
    },
    tabsCliekHandler(tab) {
      this.$nextTick(() => {
        let name = tab.name;
        let key = name + "Editor";
        if (this.$refs[key]) {
          this.$refs[key].layout();
        }

      })
    },
    editorLoaded(key) {
      let name = key + "Editor";
      this.$nextTick(() => {
        if (this.$refs[name]) {
          var v = key == 'attrs' ? JSON.stringify(this.data[key]) : (this.data[key]||"");
          this.$refs[name].setContent(v||"");
        }
      })

    }
  }
}
</script>