<template>
  <div>
    <el-form ref="form">
      <el-form-item label="子流程类型">
        <el-select v-model="Mode" name="子流程类型" style="width: 77%;">
          <el-option label="引用" value="0"></el-option>
          <el-option label="复制" value="1"></el-option>
          <el-option label="新建" value="2"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <SelectTree v-if="Mode !== '2'" :tree-data="flowData" :select.sync="selectFlow" :checked="checked" :show-search="true"></SelectTree>
  </div>
</template>
<script>
import SelectTree from "./selectTree";

export default {
  name: "SetSubFlow",
  components: {
    SelectTree,
  },
  props: {
    value: {
      type: String,
      default() {
        return "";
      },
    },
    mode: {
      type: Number,
      default() {
        return 0;
      },
    },
  },
  data() {
    return {
      flowData: [],
      selectFlow: null,
      checked: [],
      Mode: String(this.mode),
    };
  },
  created() {
    this.initFlow();
  },
  mounted() {},
  methods: {
    yes() {
      return {
        Mode: Number(this.Mode),
        SubWFID: this.Mode !== "2" ? this.selectFlow.dsfa_rm_id : null,
        SubWFName: this.Mode !== "2" ? this.selectFlow.name : null,
      };
    },
    validate() {
      if (!this.selectFlow) {
        dsf.layer.message("请选择流程", false);
        return false;
      }
      return true;
    },
    // 获取角色树
    initFlow() {
      this.$http.get("rm/list/wf", {}).then((response) => {
        if (response.data.success) {
          const data = response.data.data[0];
          const formatData = data ? this.formatData(data, "0") : null;
          this.flowData = data ? [formatData] : [];

          if (this.currentData && this.currentData.dsfa_rm_id) {
            this.checked = [this.currentData.dsfa_rm_id];
            this.selectFlow = this.currentData;
          }

          if (this.value) {
            this.checked = [this.value];
          }
        }
      });
    },
    formatData(data, disabledVaule) {
      data.label = data.name;
      data.id = data.dsfa_rm_id;
      if (disabledVaule) {
        data.disabled = data.type_value === disabledVaule ? true : false;
      }
      if (data.type_value != "4") {
        data.nocheck = true;
      }
      if (data.type_value != "4" && !data.children) {
        data.iconSkin = "_special";
      }
      if (data.children) {
        for (var i = 0; i < data.children.length; i++) {
          this.formatData(data.children[i], disabledVaule);
        }
      }
      if (this.value === data.id) {
        this.selectFlow = data;
      }
      return data;
    },
  },
};
</script>
<style lang="scss" scoped></style>
