//vant桌面浏览器支持
import '@vant/touch-emulator';
//引入js,html美化工具
import beautify from "js-beautify/js/lib/beautifier";
import DsfDesigner from './dsfDesigner.vue';
import MobileSetting from './mobileSetting.vue';
import DsfDesignerProperties from '../../propertyEditor/dsfDesignerProperties.vue'
import DsfDesignerProperty from '../../propertyEditor/dsfDesignerProperty';
import MonacoEditor from '_platform/editor/monacoEditor';
import Development from './development.vue';
import DsfUserComponentDev from './dsfUserComponentDev.vue'

import setting from '../../setting';

function install(Vue) {
  //为dsf注入美化js,html格式函数
  dsf.mix(dsf, { beautify });
  Vue.use(setting);
  Vue.component(DsfDesigner.name, DsfDesigner);
  Vue.component(DsfDesignerProperties.name, DsfDesignerProperties);
  Vue.component(DsfDesignerProperty.name, DsfDesignerProperty);
  Vue.component(MonacoEditor.name, MonacoEditor);
  Vue.component(MobileSetting.name, MobileSetting);
  Vue.component(Development.name, Development);
  Vue.component(DsfUserComponentDev.name, DsfUserComponentDev);
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

export {
  install,
  DsfDesigner,
  DsfDesignerProperty,
  DsfDesignerProperties,
  MonacoEditor,
  MobileSetting,
  Development
}
export default install;