<template>
  <div v-show="popShow" class="seat-pop-box" :style="sty">
    <ul>
      <li v-if="isExistPerson" @click="removePerson">移除占用</li>
      <li v-if="isExistPerson" @click="splicePerson">移除占用并补位</li>
      <li v-if="couldSeat !== 1" @click.left.stop="seat2work">启用</li>
      <li v-for="(item, it) in otherList" :key="it" @click.left.stop="clickOther(item)">{{ item.statusText }}</li>
    </ul>
    
    <el-dialog :visible.sync="dialogVisible" title="信息" width="300px" :modal="false" :append-to-body="true">
    
    </el-dialog>
  </div>
</template>

<script>
  import eventBus from "_platform/designer/seat/eventBus"
  import {eventBusEvent} from "_platform/designer/seat/constant";
  import SeatConfig, {leftTree, viewSeatLayout} from "_platform/public/seat_config";
  import {newRelative} from "_platform/designer/seat/func";
  
  export default {
    name: "SeatPop",
    props: {
      relativeList: {
        type: Array,
        default() {
          return []
        }
      },
      chosenItems: {
        type: Array,
        default() {
          return [];
        }
      },
      seatObj: {
        type: Object,
        default() {
          return null
        }
      },
      relativeObj: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    data() {
      return {
        dialogVisible: false,
        currentItem: {
          currentState: {},
          stateList: []
        },
        position: {},
        popShow: false,
        state: null,  //当前座位的状态
        couldSeat: 1, //当前座位是否可以入座 1可以  2不行
      }
    },
    computed: {
      sty() {
        return {
          left: this.position.x + "px",
          top: this.position.y + "px"
        }
      },
      otherList() {
        const all = this.currentItem.stateList.slice(2);
        if (this.state) {
          let index = all.findIndex(a => a.statusValue === this.state)
          if (index !== -1) all.splice(index, 1);
        }
        return all
      },
      personIdAlias() {
        return this.leftTreeConfig.leafProps.id
      },
      
      isExistPerson() {
        if (!this.chosenItems || this.chosenItems.length === 0) {
          return !!this.currentItem.person && this.currentItem.currentState.statusValue !== 3
        } else {
          return this.chosenItems.some(item => !!item.person);
        }
      },
    },
    created() {
      this.leftTreeConfig = new SeatConfig(leftTree);
      this.layoutConfig = new SeatConfig(viewSeatLayout);
    },
    mounted() {
      eventBus.on(eventBusEvent.SEAT_POP, data => {
        Object.assign(this, data);    //这里给this.currentItem 赋值
        const seatState = data.currentItem.currentState;
        if (seatState) {
          this.couldSeat = seatState.couldSeat;
          this.state = seatState.statusValue;
        } else {
          this.couldSeat = 1;
          this.state = null;
        }
        
        this.popShow = true;
      })
      eventBus.on(eventBusEvent.LEFT_CLICK_SEAT, () => {
        this.popShow = false;
        $(".active").removeClass("active")
      })
      eventBus.listen(eventBusEvent.BODY_EMPTY_CLICK, () => {
        this.popShow = false;
        $(".active").removeClass("active")
      })
    },
    methods: {
      popDialog(evt) {
        evt.stopPropagation();
        evt.preventDefault();
        this.popShow = true;
        this.dialogVisible = true
      },
      clickOther(currentState) {
        if (!this.chosenItems || this.chosenItems.length === 0) {
          const rel = this.relativeObj[this.currentItem.seatNum];
          _singleSetStatus.call(this, rel, this.currentItem, currentState)
        } else {
          for (let i = 0; i < this.chosenItems.length; i++) {
            let item = this.chosenItems[i];
            let rel = this.relativeObj[item.seatNum];
            _singleSetStatus.call(this, rel, item, currentState)
          }
          this.$emit("update:chosenItems", [])
        }
        this.popShow = false;
      },
      seat2work() {
        if (!this.chosenItems || this.chosenItems.length === 0) {
          const rel = this.relativeObj[this.currentItem.seatNum];
          rel.currentState = rel.person ? this.currentItem.stateList[1] : this.currentItem.stateList[0];
        } else {
          for (let i = 0; i < this.chosenItems.length; i++) {
            let item = this.chosenItems[i];
            let rel = this.relativeObj[item.seatNum];
            if (!rel) continue;
            rel.currentState = rel.person ? item.stateList[1] : item.stateList[0];
          }
          this.$emit("update:chosenItems", [])
        }
        this.popShow = false;
      },
      removePerson() {
        if (!this.chosenItems || this.chosenItems.length === 0) {
          let rel = this.relativeObj[this.currentItem.seatNum];
          if (rel.currentState.statusValue === 3) return;
          rel.person = null
          rel.currentState = this.currentItem.stateList[0];
        } else {
          for (let i = 0; i < this.chosenItems.length; i++) {
            let item = this.chosenItems[i];
            let rel = this.relativeObj[item.seatNum];
            if (!rel || rel.currentState.statusValue === 3) continue;
            rel.person = null
            rel.currentState = item.stateList[0];
          }
        }
      },
      splicePerson() {
        const arr = [];               //arr为可以往前补位的数据
        const havePersonArr = [];     //havePersonArr为可以往前补位并且有人入座的数据
        let startSeatNum;     //开始入座的座位号
        if (this.chosenItems?.length > 0) {
          this.chosenItems.sort((a, b) => a.seatNum - b.seatNum);
          startSeatNum = this.chosenItems[0].seatNum;
          
          for (let num in this.relativeObj) {
            let a = this.relativeObj[num];
            if (num < startSeatNum || a.currentState.couldSeat === 2) continue;
            arr.push(a);
            let index = this.chosenItems.findIndex(b => b.id === a.id);
            if (index !== -1 || !a.person) continue;
            havePersonArr.push(a)
          }
          
          //先删除这些座位
          const temp = this.relativeList.filter(a => {
            let index = arr.findIndex(b => b.id === a.id)
            return index === -1
          })
          this.$emit("update:relativeList", temp);
        } else {
          const rel = this.relativeObj[this.currentItem.seatNum];
          startSeatNum = rel.seatNum;
          // 这里依赖对象的属性顺序
          for (let num in this.relativeObj) {
            let a = this.relativeObj[num];
            if (num <= startSeatNum || a.currentState.couldSeat === 2) continue;
            arr.push(a)
            if (a.person) havePersonArr.push(a)
          }
          //先删除这些座位
          const temp = this.relativeList.filter(a => {
            if (a.id === rel.id) return false;   //删除当前选中的这个座位
            let index = arr.findIndex(b => b.id === a.id)
            return index === -1
          })
          this.$emit("update:relativeList", temp);
        }
        
        //再重新将人按顺序插入
        this.$nextTick(() => {
          let i = 0, k = 0
          while (k < havePersonArr.length) {
            const seat = this.seatObj[startSeatNum + i];
            const person = havePersonArr[k].person;
            if (this.relativeObj[startSeatNum + i]?.currentState?.couldSeat === 2) {
              i++
              continue
            }
            if (_.isFunction(this.layoutConfig.isSeatAble)) {
              let isSeatAble = this.layoutConfig.isSeatAble(seat, person)
              if (!isSeatAble) {
                i++
                continue;
              }
            }
            this.relativeList.push(newRelative(seat, person))
            i++;
            k++;
          }
        })
      },
    }
  }
  
  function _singleSetStatus(rel, targetItem, currentState) {
    if (rel) {
      rel.currentState = currentState
      if (currentState.couldSeat === 2 && currentState.statusValue !== 3 && rel.person) rel.person = null;  //如果禁用则清除座位上的人员
    } else {
      const newRelative = {
        id: targetItem.id,
        pid: targetItem.parentId,
        belong: targetItem.belong,
        currentState: currentState,
        person: null,
        seatNum: targetItem.seatNum
      }
      this.relativeList.push(newRelative);
    }
  }

</script>