<template>
  <el-container class="tree-selector-edit">
    <el-header style="padding: 10px 0px; text-align: right;">
      <DsfButton icon="iconfont icon-zidingyi" @click="add">添加</DsfButton>
      <DsfButton icon="iconfont icon-Magnifier" @click="openIcon">图标库</DsfButton>
    </el-header>
    <el-main style="padding: 0px;">
      <el-table ref="table" class="ds-table" :border="true" :data="tableData">
        <el-table-column
          label="条件"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.code" placeholder="支持表达式" />
          </template>
        </el-table-column>

        <el-table-column
          label="节点展开时图标"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input
              v-model="scope.row.iconExpand"
              placeholder="空则为无图标"
            />
          </template>
        </el-table-column>

        <el-table-column
          label="节点折叠时图标"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.iconFold" placeholder="空则为无图标" />
          </template>
        </el-table-column>

        <el-table-column
          label="图标颜色"
          :header-align="'center'"
          :align="'center'"
          width="110"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.color" placeholder="空则为原色图标" />
          </template>
        </el-table-column>

        <el-table-column
          label="操作"
          :header-align="'center'"
          :align="'center'"
          :width="120"
        >
          <template v-slot:default="scope">
            <DsfButton
              :size="'small'"
              title="插入"
              icon="hao"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="insert(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="删除"
              icon="guanbi2"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="deleted(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="上移"
              icon="shang"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="up(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="下移"
              icon="xia"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="down(scope)"
            ></DsfButton>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";
export default {
  name: "TreeIconEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      names: [],
      isOuaSelect: false,
    };
  },
  created() {
    this.tableData = this.currentData || [];
  },
  methods: {
    newTab() {
      return {
         code: "",
        iconExpand: "",
        iconFold: "",
        color: "",
      };
    },
    add() {
      // 新增
      this.tableData.push(this.newTab());
    },
    insert(scope) {
      // 插入
      this.tableData.splice(scope.$index + 1, 0, this.newTab());
    },
    deleted(scope) {
      dsf.array.remove(this.tableData, scope.row);
    },
    up(scope) {
      if (scope.$index > 0) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex - 1, 0, curr);
      }
    },
    down(scope) {
      if (this.tableData.length - 1 > scope.$index) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex + 1, 0, curr);
      }
    },
    openIcon() {
      window.open(
        dsf.url.getWebPath("$/pc/iconfont/demo_index.html"),
        "icondemo"
      );
    },
    yes() {
      return this.tableData;
    },
  },
};
</script>
