<template>
  <div class="seat-item-container normal">
    <!--seat-->
    <div
      v-for="(item, it) in myItems"
      :key="item.id"
      class="seat-box"
      :style="getItemSty(item)"
      :class="realSeatCss(item)"
      :data-id="item.id"
      :data-belong="item.type"
      :data-num="item.seatNum"
      :exist-person="!!item.person"
      :data-person="item.person? item.person[personNameAlias]: ''"
      @click.right="popMenu($event, item)"
      @click.left="mulChooseSeat($event, item)"
    >
      <div class="seat">
        <template v-if="item.person">
          <p v-if="item.width*panelSize.scale > 60" class="person">{{ item.person[personNameAlias] }}</p>
          <p v-else class="person" v-text="item.person[personNameAlias].charAt(0)"></p>
        </template>
        <p v-else>{{ item.seatTxt }}</p>
      </div>
    </div>
    
    <!--group-->
    <div
      v-for="(group, g) in myGroups"
      :key="group.id"
      class="group-box"
      :style="getItemSty(group)"
    >
      <div class="group">
        <div
          v-for="(member, m) in group.member"
          :key="member.id"
          :style="getMemberSty(member)"
          class="member"
          :class="realSeatCss(member)"
          :data-id="member.id"
          :data-pid="group.id"
          :data-num="member.seatNum"
          :exist-person="!!member.person"
          :data-person="member.person? member.person[personNameAlias]: ''"
          data-belong="group"
          @click.right="popMenu($event, group, member)"
          @click.left="mulChooseSeat($event, member, group)"
        >
          <template v-if="member.person">
            <p v-if="member.width*panelSize.scale > 60" class="person">{{ member.person[personNameAlias] }}</p>
            <p v-else class="person" v-text="member.person[personNameAlias].charAt(0)"></p>
          </template>
          
          <p v-else>{{ member.seatTxt }}</p>
        </div>
      </div>
    </div>
    
    <!--confirm-->
    <el-dialog
      title="提示"
      :visible.sync="confirm.visible"
      :append-to-body="true"
      width="30%">
      <div>{{confirm.msg}}</div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="seatCover">覆盖</el-button>
        <el-button @click="seatInsert">插入</el-button>
        <el-button @click="seatCancel">取消</el-button>
      </div>
    </el-dialog>
  
  </div>
</template>

<script>
  import {jqui} from '_platform/pc/common/importJqueryUI';
  import {mapState} from 'vuex';
  import eventBus from "_platform/designer/seat/eventBus"
  import {eventBusEvent} from "_platform/designer/seat/constant";
  import SeatConfig, {viewSeatLayout, leftTree} from "_platform/public/seat_config";
  import {newRelative} from "_platform/designer/seat/func"
  
  export default {
    name: "SeatItem",
    props: {
      //快捷键
      keyStack: {
        type: Array,
        default() {
          return []
        }
      },
      //所有座位
      items: {
        type: Array,
        default() {
          return []
        }
      },
      //所有组
      groups: {
        type: Array,
        default() {
          return []
        }
      },
      
      seatObj: {
        type: Object,
        default() {
          return null
        }
      },
      
      //所有排座情况
      relativeList: {
        type: Array,
        default() {
          return []
        }
      },
      
      //多个选中的座位
      chosenItems: {
        type: Array,
        default() {
          return []
        }
      },
      
      //所有可以入座的人员
      personObj: {
        type: Object,
        default() {
          return {};
        }
      },
      
      relativeObj: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    data() {
      return {
        confirm: {
          visible: false,
          msg: '',
          type: ''
        },
      }
    },
    computed: {
      ...mapState("seatDesign", {
        panelSize: "panelSize",
        activeId: "activeId",
      }),
      myItems() {
        const arr = _.cloneDeep(this.items);
        for (let i = 0; i < this.relativeList.length; i++) {
          for (let j = 0; j < arr.length; j++) {
            let rel = this.relativeList[i];
            let seat = arr[j];
            if (rel.id !== seat.id) continue;
            Object.assign(seat, rel);
          }
        }
        return arr;
      },
      myGroups() {
        const arr = _.cloneDeep(this.groups);
        let haveReaded = false;
        for (let i = 0; i < this.relativeList.length; i++) {
          haveReaded = false;
          for (let j = 0; j < arr.length; j++) {
            if (haveReaded) break;
            let rel = this.relativeList[i];
            let group = arr[j];
            if (group.id !== rel.pid) continue;
            for (let k = 0; k < group.member.length; k++) {
              let seat = group.member[k];
              if (seat.id !== rel.id) continue;
              Object.assign(seat, rel);
              haveReaded = true;
              break;
            }
          }
        }
        return arr;
      },
      personNameAlias() {
        return this.leftTreeConfig.leafProps.label
      },
      personIdAlias() {
        return this.leftTreeConfig.leafProps.id
      },
    },
    watch: {
      "panelSize.scale"() {
        this.items.forEach(el => {
          el.isFrozenSty = false
        })
        this.groups.forEach(el => {
          el.isFrozenSty = false;
          el.isFrozenMemberSty = false
        })
      },
      // relativeList: {
      //   handler(){
      //     this.showPeopleInteraction()
      //   },
      //   deep: true
      // },
      chosenItems(currentVal) {
        //清空所有chosen css(初始化)
        $("i.anchor").remove();
        $(".chosen").removeClass("chosen");
        
        //根据当前的 chosenItems 重新渲染
        const jud = {}, anchor = {};
        currentVal.forEach(a => {
          jud[a.id] = a;
          a.ischosenRreference && (anchor[a.id] = a);
        })
        $(".member").each(function () {
          let index = $(this).data("id");
          jud[index] && $(this).addClass("chosen")
          anchor[index] && $(this).append("<i class='anchor'></i>")
        })
        $(".seat-box").each(function () {
          let index = $(this).data("id");
          jud[index] && $(this).addClass("chosen")
        })
      }
    },
    created() {
      this.layoutConfig = new SeatConfig(viewSeatLayout);
      this.leftTreeConfig = new SeatConfig(leftTree);
    },
    beforeDestroy() {
      $(document).off("mouseup.scaleSeatItem");
      $(document).off("mousemove.scaleSeatItem");
    },
    mounted() {
      eventBus.on(eventBusEvent.KEY_CTRL_D, () => {
        
      })
      eventBus.on(eventBusEvent.KEY_DELETE, () => {
        
      })
      eventBus.listen(eventBusEvent.BODY_EMPTY_CLICK, () => {
        console.log("BODY_EMPTY_CLICK")
        this.$emit("update:chosenItems", [])
      })
      
      
      this.createInteraction()
    },
    methods: {
      mulChooseSeat(evt, item, group) {
        if (!this.layoutConfig.openMultipleChoose) return;
        $(".seat-item-container .person-info").remove();    //移除人名浮动提示框
        evt.stopPropagation();
        evt.preventDefault();
        if (this.keyStack[0] !== "ctrl" && this.keyStack[0] !== "shift") {
          let chosenIndex = this.chosenItems.findIndex(a => a.id === item.id);
          if (chosenIndex === -1) {
            eventBus.dispatch(eventBusEvent.BODY_EMPTY_CLICK)
            return;
          }
        }
        
        eventBus.emit(eventBusEvent.LEFT_CLICK_SEAT);
        
        if (this.keyStack[0] === "ctrl") {
          let index = this.chosenItems.findIndex(a => a.id === item.id);
          if (index !== -1) {
            this.chosenItems.splice(index, 1);
            if (item.ischosenRreference) item.ischosenRreference = false;
          } else {
            this.chosenItems.push(item);
          }
        } else if (this.keyStack[0] === "shift") {
          if (!group) {
            this.$emit("update:chosenItems", [item]);
          } else {
            const arr = this.chosenItems.filter(a => a.parentId === group.id)
            if (arr.length === 0) {
              item.ischosenRreference = true;   //此点为参考点
              this.chosenItems.push(item);
            } else {
              const lastObj = arr.find(a => a.ischosenRreference);    //返回第一次选中的参考点
              //如果group里面从来没有使用 shift 选中过, 则清空被选中的
              if (!lastObj) {
                const cleanArr = _.differenceBy(this.chosenItems, arr, "id");
                item.ischosenRreference = true;
                cleanArr.push(item)
                this.$emit("update:chosenItems", cleanArr);
              } else {
                //获取当前选中点和group里面选中的参考点
                let currentIndex, lastObjIndex;
                group.member.some((a, index) => {
                  if (currentIndex && lastObjIndex) return true;
                  item.id === a.id && (currentIndex = index)
                  lastObj.id === a.id && (lastObjIndex = index)
                })
                
                //清空当前选中group 里面的所有项
                const cleanArr = this.chosenItems.filter(a => a.parentId !== group.id)
                if (currentIndex === lastObjIndex) {
                  item.ischosenRreference = false;  //取消参考点
                  this.$emit("update:chosenItems", cleanArr);
                } else {
                  let temp = currentIndex < lastObjIndex ?
                    group.member.slice(currentIndex, lastObjIndex + 1) :
                    group.member.slice(lastObjIndex, currentIndex + 1);
                  
                  const result = cleanArr.concat(temp);
                  this.$emit("update:chosenItems", result);
                }
              }
            }
          }
        }
      },
      realSeatCss(item) {
        return [
          {realSeat: !/^sec/.test(item.type)}
        ]
      },
      getItemSty(item) {
        const sty = {
          width: item.width * this.panelSize.scale + "px",
          height: item.height * this.panelSize.scale + "px",
          left: item.left * this.panelSize.scale + "px",
          top: item.top * this.panelSize.scale + "px",
          transform: `rotate(${item.angle}deg)`
        }
        if (item.type !== "group") {
          sty.background = getBgSty(item);
        }
        item.style = sty;
        return sty
      },
      getMemberSty(member) {
        const sty = {
          width: member.width * this.panelSize.scale + "px",
          height: member.height * this.panelSize.scale + "px",
          left: member.left * this.panelSize.scale + "px",
          top: member.top * this.panelSize.scale + "px",
        }
        sty.background = getBgSty(member);
        return sty
      },
      popMenu(evt, item, member) {
        if (/^sec_/.test(item.type) || evt.which !== 3) return;
        
        const currentItem = item.type !== "group" ? item : member;
        
        let chosenIndex = this.chosenItems.findIndex(a => a.id === currentItem.id)
        if (chosenIndex === -1) {
          eventBus.dispatch(eventBusEvent.BODY_EMPTY_CLICK);
        }
        
        $(".active").removeClass("active");
        const targetDOM = getDOM(evt, !member);
        $(targetDOM).addClass("active");
        
        evt.preventDefault();
        evt.stopPropagation();
        let downPos = dsf.math.convertLocalCoordinate('.seat-designer-page', {x: evt.pageX, y: evt.pageY});
        setRect2Active.call(this, item);
        eventBus.emit(eventBusEvent.SEAT_POP, {
          position: {
            ...downPos
          },
          currentItem: currentItem
        })
      },
      createInteraction() {
        jqui.then(() => {
          this.$nextTick(() => {
            const self = this;
            $(".seat-item-container .realSeat").droppable({
              addClasses: false,
              tolerance: "pointer",
              over(evt, ui) {
                console.log("droppable over")
                const seatDOM = $(evt.target);
                if (seatDOM.hasClass("realSeat")) {
                  seatDOM.css({"box-shadow": "0 0 5px inset #409eff"})
                }
              },
              drop(evt, ui) {
                const seatDOM = $(evt.target);
                seatDOM.css("box-shadow", "none");
                
                if (ui.helper.attr("empty-person")) return; //没有可以入座的人员则直接返回
                
                if (seatDOM.data("belong") === "group") {
                  const groupContainer = self.myGroups.find(a => a.id === seatDOM.data("pid"));
                  self.dropSeatObj = groupContainer.member.find(a => a.id === seatDOM.data("id"));
                } else {
                  self.dropSeatObj = self.myItems.find(a => a.id === seatDOM.data("id"));
                }
                
                //选择了多个节点
                if (ui.helper.attr("multiple-choose")) {
                  const persons = [];
                  ui.helper.children("span").each(function () {
                    persons.push($(this).data("id"))
                  })
                  self.mPersonIds = persons;
                  multiple2seated.call(self);
                }
                //只选择了一个节点
                else {
                  const personId = ui.helper.data("id");
                  if (this.layoutConfig.model === "tree") {
                    self.mPersonid = personId
                    treeNode2seated(self);
                  } else if (this.layoutConfig.model === "list") {
                    self.mPersonIds = [personId];
                    multiple2seated.call(self)
                  }
                }
                
              },
              out(evt, ui) {
                const seatDOM = $(evt.target);
                seatDOM.css("box-shadow", "none");
              }
            })
            
            $(".seat-item-container").on("mouseenter", "[exist-person]", function (event) {
              if ($(this).hasClass("chosen")) return;
              if ($(this).find(".person-info")?.length > 0) return;
              let personName = $(this).attr("data-person");
              $(this).append(`<div class="person-info">${personName} <i></i></div>`)
            })
  
            $(".seat-item-container").on("mouseleave", "[exist-person]", function () {
              $(this).find(".person-info").remove()
            })
            
          })
        })
      },
      seatCover() {
        let startSeatNum = this.dropSeatObj.seatNum;
        let i = 0, k = 0;
        while (k < this.mPersonIds.length) {
          let rel = this.relativeObj[startSeatNum + i];
          let person = this.personObj[this.mPersonIds[k]];
          let seat = this.seatObj[startSeatNum + i]
          if (!seat) break;
          
          if (_.isFunction(this.layoutConfig.isSeatAble)) {
            let isSeatAble = this.layoutConfig.isSeatAble(seat, person)
            if (!isSeatAble) {
              i++
              continue;
            }
          }
          
          if (rel) {
            if (rel.currentState.couldSeat === 2) {
              i++
              continue;
            }
            rel.person = person
            rel.currentState = seat.stateList[1];
          } else {
            this.relativeList.push(newRelative(seat, person))
          }
          i++;
          k++;
        }
        
        this.confirm.visible = false;
      },
      seatInsert() {
      },
      seatCancel() {
        this.dropSeatObj = null;
        this.mPersonIds = null;
        this.mPersonid = null;
        this.confirm.visible = false;
      },
      popConfirm(msg) {
        this.confirm = {
          visible: true,
          msg: "检测到您需要入座的座位上已经存在其他人员，您希望如何处理？",
          type: "warning"
        }
      },
    },
  }
  
  //获取选中的DOM对象
  function getDOM(evt, isSeatBox) {
    const temp = $(evt.target);
    if (isSeatBox) {
      if (temp.hasClass("seat-box")) return temp;
      return temp.parents(".seat-box").eq(0);
    } else {
      if (temp.hasClass("member")) return temp;
      return temp.parents(".member").eq(0)
    }
  }
  
  //多个节点排座
  function multiple2seated() {
    const personIds = this.mPersonIds;
    //如果座位启动了多选框
    if (this.chosenItems?.length > 0) {
      const index = this.chosenItems.findIndex(a => a.id === this.dropSeatObj.id);
      //如果人员未放置在多选框
      if (index === -1) {
        dsf.layer.confirm("您已选中了部分座位，只有将人员拖入选中的座位中，才能开始依序号排座")
        return
      }
      
      this.chosenItems.sort((a, b) => a.seatNum - b.seatNum);
      
      let len = 0, seatObj = {};
      for (let seat of this.chosenItems) {
        if (seat?.currentState?.couldSeat === 2) continue;
        if (seat.person) {
          this.popConfirm();
          return;
        }
        
        let person = this.personObj[personIds[len]];
        if (_.isFunction(this.layoutConfig.isSeatAble)) {
          let isSeatAble = this.layoutConfig.isSeatAble(seat, person)
          if (!isSeatAble) continue;
        }
        seatObj[seat.id] = {
          seat: seat,
          person: person
        }
        len += 1;
        if (len === personIds.length) break;
      }
      
      simpleInsertSeat.call(this, seatObj);
    }
    //如果座位没有开启多选框
    else {
      let startNum = this.dropSeatObj.seatNum;
      for (let i = 0; i < personIds.length; i++) {
        if (this.relativeObj[startNum + i]?.person) {
          this.popConfirm();
          return;
        }
      }
      
      let i = 0, k = 0;
      while (k < personIds.length) {
        let person = this.personObj[personIds[k]];
        let rel = this.relativeObj[startNum + i];
        let seat = this.seatObj[startNum + i];
        if (!seat) return;  //如果已经没有座位了
        if (rel?.currentState?.couldSeat === 2) {
          i++;
          continue;
        }
        
        if (_.isFunction(this.layoutConfig.isSeatAble)) {
          let isSeatAble = this.layoutConfig.isSeatAble(seat, person);
          if (!isSeatAble) {
            i++
            continue;
          }
        }
        
        if (rel) {
          rel.person = person;
          rel.currentState = seat.stateList[1];
        } else {
          this.relativeList.push(newRelative(seat, person))
        }
        i++;
        k++;
      }
    }
  }
  
  
  function simpleInsertSeat(seatObj) {
    for (let i in seatObj) {
      let seat = seatObj[i].seat;
      let person = seatObj[i].person
      this.relativeList.push(newRelative(seat, person))
    }
    
    // 更新选中的items
    for (let i = 0; i < this.chosenItems.length; i++) {
      let a = this.chosenItems[i];
      if (!seatObj[a.id]) continue;
      
      let seat = seatObj[a.id].seat;
      let person = seatObj[a.id].person;
      a.currentState = seat.stateList[1];
      a.person = person
    }
  }
  
  //树节点排座
  function treeNode2seated(personId) {
    
  }
  
  //激活单个座位
  function setRect2Active(seat) {
    if (this.activeId === seat.id) return;
    //清空已选的座位
    this.$store.commit("seatDesign/changeActiveId", seat.id);
  }
  
  //获取seat背景的样式
  function getBgSty(obj) {
    let url;
    //如果只是辅助对象
    if (/^sec_/.test(obj.type)) {
      url = dsf.url.getAbsolutePath(obj.bgImg);
    } else {
      if (obj.currentState) {
        if (!obj.currentState.img) return "none"
        url = dsf.url.getAbsolutePath(obj.currentState.img);
      } else {
        const judge = _.isArray(obj.stateList) && obj.stateList[0] && obj.stateList[0].img;
        if (!judge) return "none"
        url = dsf.url.getAbsolutePath(obj.stateList[0].img)
      }
    }
    return `center / contain no-repeat url(${url})`
  }
</script>