<template>
  <el-container>
    <el-main style="padding:0px">
      <el-form ref="form" :model="form" label-width="130px">
        <el-form-item label="字体大小">
          <el-select v-model="form.fontSize">
            <el-option label="1号字(34px)" value="ds-font1"></el-option>
            <el-option label="2号字(24px)" value="ds-font2"></el-option>
            <el-option label="3号字(18px)" value="ds-font3"></el-option>
            <el-option label="4号字(16px)" value="ds-font4"></el-option>
            <el-option label="5号字(14px)" value="ds-font5"></el-option>
            <el-option label="6号字(12px)" value="ds-font6"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否加粗">
          <el-radio-group v-model="form.bold">
            <el-radio :label="true">是</el-radio>
            <el-radio :label="false">否</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="字体颜色">
          <el-input v-model="form.color" placeholder="#606266"></el-input>
        </el-form-item>
        <el-form-item label="高度">
          <el-input v-model="form.height" placeholder="auto"></el-input>
        </el-form-item>
        <el-form-item label="行间距">
          <el-select v-model="form.lineHeight">
            <el-option label="默认" value="normal"></el-option>
            <el-option label="1.2倍" value="1.2em"></el-option>
            <el-option label="1.5倍" value="1.5em"></el-option>
            <el-option label="2倍" value="2em"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="文字最大显示行数">
          <el-input-number v-model="form.lineNum" controls-position="right" :min="0" :max="5"></el-input-number><span style="display:inline-block;margin-left:10px;color:#c0c0c0">
            (0表示不省略且根据内容自动撑开容器)</span>
        </el-form-item>
        <el-form-item label="水平对齐方式">
          <el-select v-model="form.align">
            <el-option label="居左" value="left"></el-option>
            <el-option label="居中" value="center"></el-option>
            <el-option label="居右" value="right"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="垂直对齐方式">
          <el-select v-model="form.verticalAlign">
            <el-option label="居上" value="top"></el-option>
            <el-option label="居中" value="middle"></el-option>
            <el-option label="居下" value="bottom"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="边距">
          <div style="display:inline-block;margin-left:10px">
            上： <el-input-number v-model="form.paddingTop" style="width:90px" controls-position="right" :min="0" :max="20"></el-input-number>
          </div>
          <div style="display:inline-block;margin-left:10px">
            右：<el-input-number v-model="form.paddingRight" style="width:90px" controls-position="right" :min="0" :max="20"></el-input-number>
          </div>
          <div style="display:inline-block;margin-left:10px">
            下：<el-input-number v-model="form.paddingBottom" style="width:90px" controls-position="right" :min="0" :max="20"></el-input-number>
          </div>
          <div style="display:inline-block;margin-left:10px">
            左：<el-input-number v-model="form.paddingLeft" style="width:90px" controls-position="right" :min="0" :max="20"></el-input-number>
          </div>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";
export default {
  name: "DesGridLayoutCellSetting",
  mixins: [propertyDialogSetting],
  props: {
    cells: {
      type: Array,
      default() {
        return []
      }
    },
    owner: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      form: {
        fontSize: "",
        align: "left",
        verticalAlign: "middle",
        color: "#333",
        paddingTop: 0,
        paddingRight: 0,
        paddingBottom: 0,
        paddingLeft: 0,
        lineNum: 0,
        height: "auto",
        lineHeight: "normal",
        bold: false
      }
    }
  },
  computed: {

  },
  created() {
    let cell = this.cells[0] || null;
    if (cell) {
      let setting = this.owner.contentLayout.setting ? this.owner.contentLayout.setting[cell.name] : {};
      setting = setting || {};
      this.form.fontSize = setting.fontSize || "ds-font5";
      this.form.align = setting.align || "left";
      this.form.verticalAlign = setting.verticalAlign || "middle";
      this.form.color = setting.color || "";
      this.form.paddingTop = setting.paddingTop || 0
      this.form.paddingRight = setting.paddingRight || 0
      this.form.paddingBottom = setting.paddingBottom || 0
      this.form.paddingLeft = setting.paddingLeft || 0
      this.form.lineNum = setting.lineNum || 0;
      this.form.height = setting.height || 'auto'
      this.form.lineHeight = setting.lineHeight || 'normal'
      this.form.bold = setting.bold || false
    }
  },
  beforeMount() {
  },
  mounted() {
  },
  methods: {
    yes() {
      let result = {}
      _.each(this.cells, (cell) => {
        result[cell.name] = dsf.mix({}, this.form);
      });
      return result;
    }
  }
}
</script>

<style lang="scss" scoped>
.table {
  width: 100%;
  height: 100%;
  border-left: 1px solid #e6e6e6;
  border-top: 1px solid #e6e6e6;
  border-collapse: collapse;
  td {
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    height: 60px;
  }
  .size-title {
    position: relative;
    font-weight: 700;
  }
}
</style>