<template>
  <el-tabs v-model="active" type="card" style="width:100%;height:100%">
    <el-tab-pane label="业务代码" name="code" lazy style="height:100%">
      <MonacoEditor ref="extCode" :code="extCode"></MonacoEditor>
    </el-tab-pane>
    <el-tab-pane label="自定义样式" name="css" lazy style="height:100%">
      <MonacoEditor ref="css" :code="css" language="scss"></MonacoEditor>
    </el-tab-pane>
    <el-tab-pane label="模板" name="模板" lazy style="height:100%">
      <MonacoEditor ref="vueTemplate" :code="vueTemplate" language="html"></MonacoEditor>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
export default {
  name: 'Development',
  props: {
    vueTemplate: {
      type: String,
      default: ""
    },
    css: {
      type: String,
      default: ""

    },
    extCode: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      active: "code"
    }
  },
  methods: {
    yes() {
      let extCode = this.$refs.extCode ? this.$refs.extCode.getContent() : this.extCode;
      let css = this.$refs.css ? this.$refs.css.getContent() : this.css;
      let vueTemplate = this.$refs.vueTemplate ? this.$refs.vueTemplate.getContent() : this.vueTemplate;

      return {
        extCode: extCode,
        css: css,
        vueTemplate: vueTemplate,
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.el-tabs /deep/ {
  & > .el-tabs__content {
    height: calc(100% - 41px);
  }
}
</style>