<template>
  <el-tabs v-model="tabActive" stretch>
    <el-tab-pane label="常用" name="1">
      <el-collapse v-model="activeName" accordion>
        <el-collapse-item v-for="(group, $index) in inCommonUseGroup" :key="$index" :title="group.name" :name="group.name">
          <div>
            <DsfDesignerProperty v-for="(item, $idx) in group.attributes" v-show="show(item.showExpress)" :key="(item.bindControlAttr || '') + $idx" :a="item.bindControlAttr + $idx" :desc="item.desc" :data="item" :owner="owner" :bind="getBind(item)">
            </DsfDesignerProperty>
          </div>
        </el-collapse-item>
      </el-collapse>
    </el-tab-pane>
    <el-tab-pane label="全部" name="2">
      <el-collapse v-model="activeName" accordion>
        <el-collapse-item v-for="(group, $index) in data" :key="$index" :title="group.name" :name="group.name">
          <div>
            <DsfDesignerProperty v-for="(item, $idx) in group.attributes" v-show="show(item.showExpress)" :key="(item.bindControlAttr || '') + $idx" :a="item.bindControlAttr + $idx" :desc="item.desc" :data="item" :owner="owner" :bind="getBind(item)">
            </DsfDesignerProperty>
          </div>
        </el-collapse-item>
      </el-collapse>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
export default {
  name: "DsfDesignerProperties",
  props: {
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    owner: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      tabActive: "1",
      activeName: "",
      inCommonUseGroup: []
    };
  },
  watch: {
    data: {
      handler(v) {
        this.activeName = v[0] ? v[0].name : "";
        this.inCommonUseGroup = this.getInCommonUse(this.data);
      }
    },
    inCommonUseGroup(v){
      if(v.length){
        this.tabActive="1"
      }
      else{
        this.tabActive="2"
      }
    }
  },
  mounted() {
    this.inCommonUseGroup = this.getInCommonUse(this.data);
  },
  methods: {
    getInCommonUse(data) {
      let arr = [];
      _.each(data, (v) => {
        let d = [];
        let attrs = _.filter(v.attributes, (a) => {
          return a.inCommonUse
        });
        if (attrs.length) {
          let obj = dsf.mix({}, v, { "attributes": attrs })
          arr.push(obj)
        }
      });
      return arr;
    },
    getBind(item) {
      let attrExpress = item.bindControlAttr || "";
      let attrLevel = attrExpress.split(".");
      if (attrLevel.length == 1) {
        return {
          data: this.owner,
          key: attrLevel[0] || ""
        };
      } else {
        let arr = attrLevel.slice(0, attrLevel.length - 1);
        let obj = this.owner;
        let c = arr.shift();
        while (c) {
          if (dsf.isDef(obj[c])) {
            obj = obj[c];
            c = arr.shift();
          } else {
            obj = null;
            break;
          }
        }
        if (!obj) {
          obj = this.owner;
        }
        return {
          data: obj,
          key: attrLevel[attrLevel.length - 1]
        };
      }
    },
    show(express) {
      if (express) {
        return this.$eval(express, this.owner)
      }
      return true;
    }
  }
};
</script>