import { Mark } from "tiptap/dist/tiptap.common"
import { updateMark, removeMark } from "tiptap-commands/dist/commands.common"

export default class ColorMark extends Mark {

  get name() {
    return 'colormark'
  }

  get schema() {
    return {
      attrs: {
        color: {
            default: '',
        },
      },
      parseDOM: [
        {
            style: 'color',
            getAttrs: value => {
                return { color: value };
            },
        },       
      ],
      toDOM: node => {
          return ['span', {
            style: `color: ${node.attrs.color};`,
          }, 0]
      },
    }
  }

  commands({ type }) {
    return attrs => {
      if (attrs.color || attrs.background) {
        return updateMark(type, attrs)
      }
      return removeMark(type)
    }
  }

}
