<template>
  <el-container>
    <el-main style="padding:0px">
      <el-tabs v-model="tab">
        <el-tab-pane label="自定义" name="list">
          <el-main>
            <el-header style="padding:10px 0px;text-align:right">
              <DsfButton @click="addBanner">新增</DsfButton>
            </el-header>          
            <el-table class="ds-table" :border="true" :data="data.list" :highlight-current-row="true">
              <el-table-column label="图片地址" :header-align="'center'" :align="'center'">
                <template v-slot:default="scope">
                  <el-input v-model="scope.row.image" placeholder="请输入图片地址"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="标题" :header-align="'center'" :align="'center'">
                <template v-slot:default="scope">
                  <el-input v-model.number="scope.row.title" placeholder="请输入标题"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="描述" :header-align="'center'" :align="'center'">
                <template v-slot:default="scope">
                  <el-input v-model.number="scope.row.desc" placeholder="请输入描述"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="链接" :header-align="'center'" :align="'center'">
                <template v-slot:default="scope">
                  <el-input v-model.number="scope.row.url" placeholder="请输入图片链接"></el-input>
                </template>
              </el-table-column>                     
              <el-table-column label="操作" :header-align="'center'" :align="'center'" :width="120">
                <template v-slot:default="scope">
                  <DsfButton :size="'small'" title="插入" icon="hao" btn-style="icon-text" style="min-width:auto;margin-left:5px" @click="insert(scope)"></DsfButton>
                  <DsfButton :size="'small'" title="删除" icon="guanbi2" btn-style="icon-text" style="min-width:auto;margin-left:5px" @click="deleted(scope)"></DsfButton>
                  <DsfButton :size="'small'" title="上移" icon="shang" btn-style="icon-text" style="min-width:auto;margin-left:5px" @click="up(scope)"></DsfButton>
                  <DsfButton :size="'small'" title="下移" icon="xia" btn-style="icon-text" style="min-width:auto;margin-left:5px" @click="down(scope)"></DsfButton>
                </template>
              </el-table-column>
            </el-table>             
          </el-main>
        </el-tab-pane>
        <el-tab-pane label="接口" name="url">
          <el-input v-model="data.url" placeholder="请输入接口地址"></el-input>
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";
function createBannerItem() {
  return {
    title: '',
    desc: '',
    image: '',
    url: ''
  }
}
export default {
  name: "DesMobileBannerEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      data: {
        url: '',
        list: [],
      },
      tab: 'list',
    }
  },
  created() {
    const { url, list } = this.owner.$$getComponent();
    this.data.url = url;
    this.data.list = dsf.mix(true, [], list);
    this.tab = url ? 'url' : 'list';
    if(!this.data.url && this.data.list.length === 0) {
      this.addBanner();
    }
  },
  methods: {
    addBanner() {
      this.data.list.push(createBannerItem());
    },
    vaildate() {
      let msg = '';
      if(this.tab === 'url' && !this.data.url) {
        msg = '请输入url';
      } else if(this.tab === 'list') {
          const bool = this.data.list.every(it => !!it.image);
          if(!bool) {
            msg = '图片地址不能为空';
          }
      }
      return msg;
    },
    deleted(scope) {
      this.data.list.splice(scope.$index, 1);
    },
    up(scope) {
      if (scope.$index > 0) {
        const arr = this.data.list.slice(scope.$index - 1, scope.$index + 1).reverse();
        this.data.list.splice(scope.$index - 1, 2, ...arr);
      }
    },
    down(scope) {
      if (this.data.list.length - 1 > scope.$index) {
        const arr = this.data.list.slice(scope.$index, scope.$index + 2).reverse();
        this.data.list.splice(scope.$index, 2, ...arr);
      }
    },
    insert(scope) {
      this.data.list.splice(scope.$index + 1, 0, createBannerItem());
    },    
    yes() {
      const msg = this.vaildate();
      if(msg) {
        dsf.layer.message(msg, false);
        throw new Error(msg);
      }
      if(!this.data.url) {
        this.owner.list = dsf.mix(true, [], this.data.list);
      } else {
        this.owner.url = this.data.url;
      }
    },
  }
}
</script>