<template>
  <div
    class="events"
    :style="{
      top: isSub ? toPx(parentNode.imgHeight + 2 * parentNode.padding) : 0,
    }"
  >
    <div
      v-for="ev in events"
      v-show="!ev.hide"
      :key="ev.id"
      class="ds_tool_event_drag tool_event"
      :class="{ selected: checked === ev }"
      :style="{ width: ev.width + 'px', height: ev.height + 'px', top: toPx(ev.point.y), left: toPx(ev.point.x) }"
      :draggable="!isDrawLine"
      :data-id="ev.id"
      @mousedown="selectEvent($event, ev)"
      @click="selectEvent($event, ev)"
      @dragstart="drag($event, ev)"
      @touchstart="drag($event, ev)"
    >
      <img
        :src="ev.url"
        :class="{ selected: checked === ev }"
        :style="{
          width: toPx(ev.img.width),
          height: toPx(ev.img.height),
        }"
        draggable="false"
      />
    </div>
  </div>
</template>
<script>
import { toPx } from "./common";
export default {
  name: "Events",
  props: {
    nodes: {
      type: Array,
      default() {
        return [];
      },
    },
    events: {
      type: Array,
      default() {
        return [];
      },
    },
    isDrawLine: {
      type: Boolean,
      default() {
        return false;
      },
    },
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    isSub: {
      // 子流程
      type: Boolean,
      default() {
        return false;
      },
    },
    parentNode: {
      type: Object,
      default() {
        return null;
      },
    },
    config: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      current: null,
      crashNode: null,
    };
  },
  created() {},
  mounted() {
    this.$bus.$on(`showSubNode${this.isSub ? this.parentNode.id : ""}`, this.showSubNode);
    this.$bus.$on(`nodeMove${this.isSub ? this.parentNode.id : ""}`, this.nodeMove);
    // this.$bus.$on(`globalMove${this.isSub ? this.parentNode.id : ''}`, this.globalMove);
  },
  methods: {
    toPx,
    drag(e, item) {
      e.dataTransfer && e.dataTransfer.setDragImage(new Image(), 0, 0); // 设置 拖动背景图为空
      if (e.type === "dragstart") {
        //根据e.type判断是PC端还是移动端
        e = event ? event : e;
      } else {
        if (event.touches) {
          e = event.touches[0] ? event.touches[0] : event;
        }
      }
      const offset = {
        x: e.offsetX,
        y: e.offsetY,
      };
      this.$bus.$emit("update-checked", item);
      this.current = { ...item, offset };
      this.$bus.$emit("dragCloneEvent", e, this.current);
    },
    selectEvent(e, ev) {
      e.stopPropagation();
      this.$bus.$emit("update-checked", ev);
      if (this.isDrawLine) {
        const event = `startDraw${this.isSub ? this.parentNode.id : ""}`;
        setTimeout(() => {
          this.$bus.$emit(event, e, ev, this.parentNode);
        }, 0);
      }
    },
    nodeMove(node) {
      if (node && this.events && this.events.length > 0) {
        this.crashNode = node;
        this.$store.commit("flow/setPointsByNode", { node, obj: this.events[0] });
        const points = this.$store.getters["flow/getPoints"](node.id);
        let hasEvents = [];
        const scale = this.$store.state.flow.page.scale;
        points.forEach((point) => {
          this.events.forEach((item) => {
            if (node.id === item.nodeId && point.direction === item.direction && point.index === item.index) {
              item = Object.assign(item, point);
              item.point.x = item.point.x / scale;
              item.point.y = item.point.y / scale;
              if (item.out && item.out.length > 0) {
                this.$bus.$emit(`eventMove${this.isSub ? this.parentNode.id : ""}`, item);
              }
              hasEvents.push(point);
            }
          });
        });
        // 删除已经有事件的点
        hasEvents.forEach((item) => {
          const index = points.indexOf(item);
          this.$store.commit("flow/deletePoint", { id: node.id, index });
        });
      }
    },
    showSubNode(node) {
      this.events.forEach((ev) => {
        if (ev.nodeId === node.id) {
          ev.hide = !node.open.show;
          if (ev.out && ev.out.length > 0) {
            this.$bus.$emit("hideLines", ev.out, !node.open.show);
          }
        }
      });
    },
    globalMove(current_p, x_move, y_move) {
      if (!current_p) {
        return;
      }
      for (let i = 0; i < this.events.length; i++) {
        let event = this.events[i];
        if (x_move) {
          event.point.x += current_p.x;
        }
        if (y_move) {
          event.point.y += current_p.y;
        }
        if (x_move || y_move) {
          // node.render();
        }
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.events {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
</style>
