<template>
  <MonacoEditor
    ref="editor"
    :language="language"
    @mounted="editorLoaded"
  ></MonacoEditor>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";

export default {
  name: "DesCodeEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      code: "",
      language: "", //编辑器语言
      language_format: "", //绑定的数据格式
    };
  },
  created() {
    this.language_format = this.config.language;
    this.language = this.language_format || "javascript";
    if (this.language_format == "jsonString") {
      this.language = "json";
    }
  },
  mounted() {},
  methods: {
    yes() {
      let res = this.$refs.editor.getContent(),
        format = this.language_format;
      if (format == "html") {
        return dsf.escapeFilter(res);
      } else if (format == "json") {
        return JSON.parse(res);
      } else {
        return this.$refs.editor.getContent();
      }
    },
    editorLoaded() {
      let v = this.currentData,
        format = this.language_format;
      if (format == "html") {
        v = dsf.unescapeHTML(v);
      } else if (format == "json") {
        v = JSON.stringify(v);
      }
      this.$refs.editor.setContent(v);
    },
  },
};
</script>
