<template>
  <el-container style="height: 100%">
    <el-aside style="width: 500px; overflow: auto">
      <el-container>
        <el-header style="padding: 0px 0px 10px 0px; text-align: right; height: auto">
          <DsfButton size="small" icon="sousuofangda" @click="openSysButtonSelect">增加系统按钮</DsfButton>
          <DsfButton size="small" icon="zidingyi" @click="addCustomButton">新增自定义按钮</DsfButton>
          <DsfButton size="small" icon="Magnifier" type="plain" @click="openIcon">查看按钮图标</DsfButton>
        </el-header>
        <el-main style="padding: 0px; height: 100%">
          <el-table border :show-header="true" :data="tableData" :highlight-current-row="true" @row-click="rowClickHandler">
            <el-table-column label="#" type="index" align="center"></el-table-column>
            <el-table-column label="显示文字" prop="text">
              <template v-slot:default="scope">{{ scope.row.text || scope.row.title }}</template>
            </el-table-column>
            <el-table-column label="名称" prop="caption"></el-table-column>
            <el-table-column label="图标" prop="text">
              <template v-slot:default="scope">
                <DsfButton btn-style="icon-text" size="small" :icon="getIcon(scope.row.icon)"></DsfButton>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template v-slot:default="scope">
                <DsfButton btn-style="icon-text" icon="shanchu" size="small" title="删除" @click="removeButton(scope.row)"></DsfButton>
                <DsfButton btn-style="icon-text" icon="shang" size="small" title="上移" @click="up(scope.row)"></DsfButton>
                <DsfButton btn-style="icon-text" icon="xia" size="small" title="下移" @click="down(scope.row)"></DsfButton>
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-aside>
    <el-main style="padding: 0px">
      <el-container>
        <el-main>
          <el-form :model="formModel" label-width="120px">
            <el-form-item label="名称">
              <el-input v-model="formModel.caption" disabled></el-input>
            </el-form-item>
            <el-form-item label="别名">
              <el-input v-model="formModel.alias" :disabled="!allowEdit()"></el-input>
            </el-form-item>
            <el-form-item label="显示文字">
              <el-input v-model="formModel.text" :disabled="!allowEdit()"></el-input>
            </el-form-item>
            <el-form-item label="鼠标悬浮提示">
              <el-input v-model="formModel.title" :disabled="!allowEdit()"></el-input>
            </el-form-item>
            <el-form-item label="图标">
              <el-input v-model="formModel.icon" :disabled="!allowEdit()"></el-input>
            </el-form-item>
            <el-form-item label="系统按钮">
              <span>{{ formModel.isSystem ? "是" : "否" }}</span>
            </el-form-item>
            <el-form-item label="动作">
              <el-input v-model="formModel.actionName" :disabled="!allowEdit() || formModel.isSystem"></el-input>
            </el-form-item>
            <el-form-item label="风格">
              <el-select v-model="formModel.btnStyle" :disabled="!allowEdit()">
                <el-option value="" label="普通按钮"></el-option>
                <el-option value="icon-text" label="图标按钮"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="类型">
              <el-select v-model="formModel.type" :disabled="!allowEdit()">
                <el-option value="" label="主要按钮"></el-option>
                <el-option value="plain" label="次要按钮"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="上传模板" v-if="formModel.isTemplateDownload">
              <el-upload
                class="ds-button-update-template"
                :multiple="false"
                :headers="headersParam"
                :action="setRequestUrl"
                :file-list="fileList"
                :on-success="handleSuccess"
                :on-error="handleError"
                :on-change="handleChange"
              >
                <DsfButton size="small">模板上传</DsfButton>
              </el-upload>
            </el-form-item>
            <el-form-item v-if="isUpBtn(formModel)" label="是否是上传按钮">
              <el-switch v-model="formModel.isUpload" :disabled="!allowEdit()" @change="uploadChange"></el-switch>
            </el-form-item>
            <el-form-item label="显示条件">
              <el-input v-model="formModel.if" :disabled="!allowEdit()"></el-input>
              <span style="color: #f56c6c">注：满足该条件时按钮显示</span>
            </el-form-item>
            <el-form-item label="禁用条件">
              <el-input v-model="formModel.disabledFactor" :disabled="!allowEdit()"></el-input>
              <span style="color: #f56c6c">注：满足该条件时按钮禁用</span>
            </el-form-item>
            <el-form-item v-if="formModel.actionName === 'system_flow_send'" label="环节发送后事件">
              <el-input v-model="formModel.flowSendOverEvent" :disabled="!allowEdit()"></el-input>
              <span style="color: #f56c6c"> 注：此事件用于二次开发，设置此事件后，发送流程页面发送完结后不自动关闭 </span>
            </el-form-item>
            <!-- <el-form-item label="是否被折叠">
              <el-switch v-model="formModel.isMore" :disabled="!allowEdit()"></el-switch>
            </el-form-item> -->
            <el-form-item v-if="formModel.actionName === 'system_flow_send' || formModel.actionName === 'system_batch_flow_send'" label="文件已办结">
              <el-input v-model="formModel.completed" :disabled="!allowEdit()"></el-input>
              <span style="color: #f56c6c">注：默认为“文件已办结”</span>
            </el-form-item>
            <el-form-item v-if="formModel.actionName === 'system_flow_send' || formModel.actionName === 'system_batch_flow_send'" label="分环节发送">
              <el-switch v-model="formModel.isLinkSend" :disabled="!allowEdit()"></el-switch>
            </el-form-item>
            <el-form-item v-if="formModel.actionName === 'system_flow_send' || formModel.actionName === 'system_batch_flow_send'" label="按钮折叠">
              <el-switch v-model="formModel.isLinkSendFold" :disabled="!allowEdit()"></el-switch>
            </el-form-item>
            <el-form-item
              v-if="formModel.actionName === 'system_flow_send' || formModel.actionName === 'system_batch_flow_send'"
              label="弹窗确认发送"
            >
              <el-switch v-model="formModel.secondConfirm" :disabled="!allowEdit()"></el-switch>
            </el-form-item>
            <el-form-item
              v-if="formModel.secondConfirm && (formModel.actionName === 'system_flow_send' || formModel.actionName === 'system_batch_flow_send')"
              label="弹窗确认提示语"
            >
              <el-input v-model="formModel.secondConfirmText" :disabled="!allowEdit()"></el-input>
              <span style="color: #f56c6c">注：默认为“确认提交？”</span>
            </el-form-item>
            <el-form-item v-if="isDblClickBind" label="绑定列表双击">
              <el-switch v-model="formModel.isDblClick" :disabled="!allowEdit()" @change="dblClickChange"></el-switch>
            </el-form-item>
            <el-form-item v-if="isDblClickBind" label="快捷链接">
              <el-select v-model="formModel.linkBind" :disabled="!allowEdit()">
                <el-option label="无" value=""></el-option>
                <el-option v-for="(op, $index) in links" :label="op.label" :value="op.value" :key="$index"></el-option>
              </el-select>
              <!-- <el-switch v-model="formModel.linkBind" :disabled="!allowEdit()" @change="dblClickChange"></el-switch> -->
            </el-form-item>
            <template v-for="(param, $index) in formModel.params">
              <el-form-item :key="$index" :label="'参数' + ($index + 1)">
                <el-input v-model="param.value" :placeholder="param.desc + ' 默认值:' + (param.default || '无')" :disabled="!allowEdit()">
                  <!-- <DsfIcon slot="suffix" :name="js"></DsfIcon> -->
                  <i slot="suffix" class="iconfont icon-js" style="cursor: pointer" title="代码编辑" @click="openEditor(param, !allowEdit())"></i>
                </el-input>
              </el-form-item>
            </template>
          </el-form>
        </el-main>
      </el-container>
      <el-dialog title="选择系统按钮" :visible.sync="sysButtonSelect" width="500px" append-to-body>
        <template>
          <div style="height: 500px; overflow: auto">
            <el-tree
              ref="tree"
              :data="treeData"
              :props="defaultProps"
              node-key="key"
              :default-checked-keys="[]"
              default-expand-all
              show-checkbox
            ></el-tree>
          </div>
        </template>
        <template v-slot:footer>
          <dsf-button @click="addSysButton">确定</dsf-button>
          <dsf-button type="plain" @click="closeSysButtonSelect">取消</dsf-button>
        </template>
      </el-dialog>
    </el-main>
  </el-container>
</template>
<script>
let rfunction = /^\s*function(\s+\w*)?\(.*\)\s*{/g;
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";
import { getPcButtons, getMobileButtons } from "../../register/buttonRegister";

import buttonParams from "_platform/designer/page/buttonParams.txt";
export default {
  name: "DesButtonEdit",
  mixins: [propertyDialogSetting],
  props: {
    defaultShowText: {
      type: Boolean,
      default: true,
    },
    defaultButtonStyle: {
      type: String,
      default: "",
    },
    isDblClickBind: {
      type: Boolean,
      default: false,
    },
    isMobile: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      tableData: [],
      formModel: {},
      sysButtonSelect: false,
      treeData: [],
      defaultProps: {
        label: "text",
        children: "children",
      },
      headersParam: {
        "X-XSRF-TOKEN": dsf.getCookie("XSRF-TOKEN") || "",
      },
      fileList: [],
      links: [],
    };
  },
  created() {
    let data = dsf.mix(true, [], this.currentData);
    _.each(data, (el) => {
      if (dsf.isUnDef(el.id)) {
        el.id = dsf.uuid();
      }
    });
    this.tableData = data;
    if (!this.isMobile) {
      this.treeData = this.convertTree(getPcButtons());
    } else {
      this.treeData = this.convertTree(getMobileButtons());
    }
    if (this.isDblClickBind) {
      dsf.designer.recursionLayoutTree(this.owner, null, null, (child) => {
        if (child != this && child.isFormControl && child.metadata_fullcode) {
          if (child.designId) {
            this.links.push({
              label: child.label ? child.label + "(" + child.metadata_fullcode + ")" : child.metadata_fullcode,
              value: child.designId,
            });
          }
        }
      });
    }
  },
  computed: {
    setRequestUrl() {
      return dsf.url.getWebPath("file/upload?db=1");
    },
  },
  mounted() {},
  methods: {
    isUpBtn(btn) {
      return btn.actionName !== "system_flow_send" && btn.actionName !== "system_batch_flow_send" && btn.actionName !== "system_template_download";
    },
    convertTree(buttons) {
      let tree = [],
        treeMap = {};
      _.each(buttons, (el) => {
        if (!treeMap[el.moduleName]) {
          treeMap[el.moduleName] = {
            text: el.moduleName,
            type: "group",
            key: el.moduleName,
            children: [],
          };
          tree.push(treeMap[el.moduleName]);
        }
        let pnode = treeMap[el.moduleName];
        let groupName = el.moduleName + "-" + el.groupName;
        if (!treeMap[groupName]) {
          treeMap[groupName] = {
            text: el.groupName,
            type: "group",
            key: groupName,
            children: [],
          };
          pnode.children.push(treeMap[groupName]);
        }
        el.button.key = groupName + "-" + el.button.text;
        treeMap[groupName].children.push(el.button);
      });
      return tree;
    },
    openSysButtonSelect() {
      if (this.$refs.tree) {
        this.$refs.tree.setCheckedKeys([]);
      }
      this.sysButtonSelect = true;
    },
    allowEdit() {
      if (dsf.isEmptyObject(this.formModel)) {
        return false;
      }
      return true;
    },
    addSysButton() {
      let nodes = this.$refs.tree.getCheckedNodes(true);
      let buttons = _.map(nodes, (node) => {
        let btn = this.createButton(node, true);
        if (!this.defaultShowText) {
          btn.text = "";
        }
        if (this.defaultButtonStyle) {
          btn.btnStyle = this.defaultButtonStyle;
        }
        if (!btn.icon) {
          btn.icon = "icon-shezhi";
        }
        if (!btn.title && btn.text) {
          btn.title = btn.text;
        }
        return btn;
      });
      this.tableData.push(...buttons);
      this.sysButtonSelect = false;
    },
    addCustomButton() {
      let button = this.createButton({
        caption: "自定义按钮",
        text: !this.defaultShowText ? "" : "新增按钮",
        btnStyle: this.defaultButtonStyle ? this.defaultButtonStyle : "",
        icon: "icon-shezhi",
      });
      this.tableData.push(button);
    },
    closeSysButtonSelect() {
      this.sysButtonSelect = false;
    },
    yes() {
      _.each(this.tableData, (el) => {
        if (dsf.isUnDef(el.id)) {
          el.id = dsf.uuid();
        }
      });
      return this.tableData;
    },
    rowClickHandler(row) {
      this.formModel = row;
    },
    createButton(obj, isSystem) {
      let c = {
        caption: "",
        text: "",
        alias: "",
        icon: "",
        btnStyle: "",
        title: "",
        isSystem: isSystem,
        actionName: "",
        if: "",
        flowSendOverEvent: "",
        type: "",
        isUpload: false,
        // isMore: false,
        isLinkSend: false,
        isLinkSendFold: false,
        disabled: false,
        isDblClick: false,
        linkBind: "",
        params: [],
      };
      let button = dsf.mix(true, {}, c, obj);
      return button;
    },
    openIcon() {
      window.open(dsf.url.getWebPath("$/pc/iconfont/demo_index.html"), "icondemo");
    },
    getIcon(icon) {
      let temp = "icon-";
      if (icon.startsWith(temp)) {
        return icon.substr(temp.length);
      }
      return icon;
    },
    removeButton(button) {
      dsf.array.remove(this.tableData, button);
    },
    up(button) {
      let index = _.findIndex(this.tableData, (b) => b == button);
      if (index > 0) {
        let targetButon = this.tableData[index - 1];
        this.$set(this.tableData, index - 1, button);
        this.$set(this.tableData, index, targetButon);
      }
    },
    down(button) {
      let index = _.findIndex(this.tableData, (b) => b == button);
      if (index > 0) {
        let targetButon = this.tableData[index + 1];
        this.$set(this.tableData, index + 1, button);
        this.$set(this.tableData, index, targetButon);
      }
    },
    dblClickChange(v) {
      if (v === true) {
        _.each(this.tableData, (row) => {
          if (this.formModel != row) {
            row.isDblClick = false;
          }
        });
      }
    },
    uploadChange(v) {
      if (v === true) {
        this.formModel.params = [
          { value: "", desc: "导入接口地址", default: "/file/upload" },
          { value: "", desc: "导入参数", default: "" },
          { value: "", desc: "可上传的文件类型", default: "xlsx|xls" },
        ];
        // _.each(this.tableData, (row) => {
        //   if (this.formModel != row) {
        //     row.isDblClick = false;
        //   }
        // });
      } else {
        this.formModel.params = [];
      }
    },
    openEditor(param, disabled) {
      if (disabled) {
        return;
      } else {
        let v = param.value.trim();
        if (v) {
          if (!this.testCodeBlock(param)) {
            v = buttonParams.replace("$$0$$", "return " + JSON.stringify(v));
          }
        } else {
          v = buttonParams.replace("$$0$$", "return ''");
        }
        dsf.layer.pc.openDialog({
          title: "代码编辑",
          fullscreen: true,
          overflow: "hidden",
          content: "MonacoEditor",
          params: {
            code: v,
            language: "javascript",
          },
          btns: [
            {
              text: "确定",
              handler: (component) => {
                param.value = component.getContent();
              },
            },
            {
              text: "取消",
            },
          ],
        });
      }
    },
    testCodeBlock(param) {
      let v = param.value.trim();
      if (v) {
        rfunction.lastIndex = 0;
        return rfunction.test(v);
      }
      return false;
    },
    //上传截取
    handleChange(file, fileList) {
      this.fileList = fileList.slice(-1);
    },
    //上传成功
    handleSuccess(response) {
      this.loading = false;
      if (response.success && response.state == 20000) {
        let file = response.data[0];
        this.formModel.params[0].value = file.id;
        this.formModel.params[1].value = file.originalFileName;
      }
    },
    //上传失败
    handleError() {
      this.loading = false;
      dsf.layer.message("上传失败", false);
    },
  },
};
</script>
