import { Mark } from "tiptap/dist/tiptap.common"
import { updateMark } from "tiptap-commands/dist/commands.common"

export default class Align extends Mark {

  get name() {
    return 'align'
  }

  get defaultOptions() {
    return {
      align: ['left', 'center', 'right'],
    }
  }  

  get schema() {
    return {
      attrs: {
        align: {
          default: 'left',
        },
      },    
      content: 'inline*',
      defining: true,
      draggable: false,
      parseDOM : [{
        style: 'text-align',
        getAttrs: align => ({ align })        
      }],            
      toDOM: node => {
          return ['span', {
            style: `text-align: ${node.attrs.align};display: block`
          }, 0]
      },
    }
  }

  commands({ type }) {
    return (attrs) => updateMark(type, attrs);
  } 

}
