<template>
  <div class="designer_tools">
    <DsfButton
      v-for="(button, $index) in layout.buttons"
      :key="$index"
      :icon="button.icon"
      :text="button.text"
      btn-style="icon-text"
      class=""
      @click="button.handler($event, button)"/>
    <width-input v-show="false" :canvas-set="canvasSet" :has-bg="hasBg"></width-input>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  import widthInput from "_platform/designer/seat/seatDesign/widthInput";
  import eventBus from "_platform/designer/seat/eventBus";
  import {eventBusEvent} from "_platform/designer/seat/constant";
  import {newRelative} from "_platform/designer/seat/func";
  import SeatConfig, {viewSeatLayout} from "_platform/public/seat_config";
  import mime from "_platform/utils/filemime";
  
  export default {
    name: "TopTools",
    props: {
      backgroundImage: {
        type: Object,
        default() {
          return {
            url: "",
            width: 0,
            height: 0,
          }
        }
      },
      canvasSet: {
        type: Object,
        default() {
          return {
            width: undefined,
            height: undefined,
          }
        }
      },
      hasBg: {
        type: Boolean,
        default: false
      },   //是否有底图
      //所有排座情况
      relativeList: {
        type: Array,
        default() {
          return []
        }
      },
      
      seatObj: {
        type: Object,
        default() {
          return null
        }
      },
      relativeObj: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    components: {
      widthInput
    },
    data() {
      return {
        //普通情况下的按钮
        normalButtons: [
          {text: "保存", icon: "baocun", handler: this.save},
          {text: "自动排座", icon: "zidongpipei", handler: this.autoSeat},
          {text: "清空排座", icon: "guanbi2", handler: this.clearSeat},
          {text: "自动补位", icon: "zhiding2", handler: this.emptySeatUtilize},
          {text: "底图复位", icon: "shuaxin", handler: this.restoreLayout},
          {text: "导出", icon: "yunduanxiazai", handler: this.exportImage},
        ],
        layout: {
          buttons: [],
        },
      }
    },
    
    computed: {
      ...mapState('seatDesign', [
        "compelPanelSize",
      ])
    },
    watch: {},
    created() {
      this.layout.buttons = this.normalButtons;
      this.layoutConfig = new SeatConfig(viewSeatLayout);
    },
    methods: {
      save() {
        this.$emit("save");
      },
      restoreLayout() {
        const panelSize = {
          width: this.compelPanelSize.width,
          height: this.compelPanelSize.height,
          scale: 1,
          left: 0,
          top: 0,
        }
        this.$store.commit("seatDesign/changePanelSize", panelSize);
      },
      autoSeat() {
        let needPop = false;
        for (let i = 0; i < this.relativeList.length; i++) {
          let rel = this.relativeList[i];
          //有人，且该座位未被锁定
          if (rel.person && rel.currentState.couldSeat !== 2) {
            needPop = true
            break;
          }
        }
        
        if (needPop) {
          dsf.layer.confirm({
            title: '提醒',
            message: "该操作会清除当前所有已排人员重新排位，是否要继续?"
          }).then(() => {
            eventBus.emit(eventBusEvent.AUTO_SEAT)
          }).catch(() => {
          
          });
        } else {
          eventBus.emit(eventBusEvent.AUTO_SEAT)
        }
      },
      clearSeat() {
        dsf.layer.confirm({
          title: '提醒',
          message: "该操作将清除当前所有排座的信息,是否要继续?"
        }).then(() => {
          this.$emit("clear");
        }).catch(() => {
          
        });
        
      },
      emptySeatUtilize() {
        let min;
        for (let i = 0; i < this.relativeList.length; i++) {
          let rel = this.relativeList[i];
          if (!rel.person) continue;
          min = rel.seatNum < min || !min ? rel.seatNum : min
        }
        
        const arr = [];
        const havePersonArr = [];
        for (let num in this.relativeObj) {
          let rel = this.relativeObj[num];
          if (num < min || rel.currentState.couldSeat === 2) continue;
          arr.push(rel);
          if (rel.person) havePersonArr.push(rel)
        }
        
        //先删除这些座位
        const temp = this.relativeList.filter(a => {
          let index = arr.findIndex(b => b.id === a.id)
          return index === -1
        })
        this.$emit("update:relativeList", temp);
        
        //再重新将人按顺序插入
        this.$nextTick(() => {
          let i = 0, k = 0
          while (k < havePersonArr.length) {
            const seat = this.seatObj[min + i];
            const person = havePersonArr[k].person;
            if (this.relativeObj[min + i]?.currentState?.couldSeat === 2) {
              i++
              continue
            }
            if (_.isFunction(this.layoutConfig.isSeatAble)) {
              let isSeatAble = this.layoutConfig.isSeatAble(seat, person)
              if (!isSeatAble) {
                i++
                continue;
              }
            }
            this.relativeList.push(newRelative(seat, person))
            i++;
            k++;
          }
        })
        
        
      },
      exportImage() {
      }
    }
  }
</script>