<template>
  <el-container style="height:100%">
    <el-aside width="25%" style="overflow:auto;height:100%">
      <div style="overflow:auto;height:100%">
        <el-tree :data="treeData" :props="defaultProps" default-expand-all>
          <span slot-scope="{node,data}" class="custom-tree-node" @dblclick="toList(node,data)">
            <i class="iconfont icon-biaodanzujian-shurukuang" style="margin-right:5px;"></i>
            <span>{{ node.label }}</span>
            <span v-if="data.field" style="color:red">(字段:{{ data.field }})</span>
          </span>
        </el-tree>
      </div>
    </el-aside>
    <el-main style="padding:0px">
      <el-container>
        <el-main style="padding-top:0px">
          <el-tabs type="card" v-model="currentRule" addable @tab-add="tabAddHandler" @tab-remove="tabRemoveHandler">
            <el-tab-pane :label="key" :name="key" v-for="(rule,key) in rules" :key="key" :closable="key!=='rule-1'">
              <el-table :data="rule" stripe :highlight-current-row="true" :row-key="getRowId" :tree-props="{children: 'children'}">
                <el-table-column width="30" align="center"></el-table-column>
                <el-table-column label="元数据代码" width="250">
                  <template v-slot:default="scope">
                    <span>{{scope.row.table}}.{{scope.row.code}}</span>
                  </template>
                </el-table-column>
                <el-table-column prop="currentValueAttribute" label="值属性" width="150"></el-table-column>
                <el-table-column label="描述">
                  <template v-slot:default="scope">
                    <el-input v-model="scope.row.caption" placeholder="相关文字描述"></el-input>
                  </template>
                </el-table-column>
                <el-table-column label="格式化">
                  <template v-slot:default="scope">
                    <el-input v-model="scope.row.format" placeholder="服务器端格式化函数"></el-input>
                  </template>
                </el-table-column>
                <el-table-column label="操作" width="130px" align="center" header-align="center">
                  <template v-slot:default="scope">
                    <template>
                      <dsf-button v-if="scope.row.searchType=='enum'" title="选项配置" size="small" btn-style="icon-text" icon="shezhi" @click.stop="settingEnumItems(scope.row)"></dsf-button>
                      <dsf-button title="删除" size="small" btn-style="icon-text" icon="guanbi2" @click.stop="remove(scope.row)"></dsf-button>
                      <dsf-button title="上移" size="small" btn-style="icon-text" icon="shang" @click.stop="up(scope.row)"></dsf-button>
                      <dsf-button title="下移" size="small" btn-style="icon-text" icon="xia" @click.stop="down(scope.row)"></dsf-button>
                    </template>
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </el-main>
      </el-container>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";
export default {
  name: "DesExportOptions",
  mixins: [propertyDialogSetting],
  data() {
    return {
      currentRule: "",
      rules: {
        "rule-1": []
      },
      // tableData: [],
      treeData: [],
      defaultProps: {
        children: "children",
        label: "name"
      },
      options: [
        { label: "选项枚举", value: "enum", defaultField: ["value"] },
        { label: "文本", value: "text" },
        { label: "日期时间", value: "datetime" }
      ]
    };
  },
  created() {},
  async mounted() {
    let grid = this.owner.$$getComponent();
    let metadata = grid.$options.design.metadata.create.call(grid);
    if (!metadata || metadata.length <= 1) {
      dsf.layer.pc.message("请将要展示的元数据拖动到列表中,如果是子表请保存布局后再试", false);
      return;
    }
    let metadatakey = metadata[1].metadata.at;
    if (!metadatakey) {
      dsf.layer.pc.message("请将要展示的元数据拖动到列表中,如果是子表请保存布局后再试", false);
      return;
    }
    this.treeData = await this.getTreeData(metadata[1].metadata, metadata[1].metadata.rm_type || 3);
    if (dsf.type(this.currentData) == "array") {
      this.rules = {
        "rule-1": this.currentData
      };
    } else {
      this.rules = this.currentData;
    }
    let allRules = Object.keys(this.rules);
    this.currentRule = allRules[0] ? allRules[0] : "rule-1";
    // this.tableData = (this.currentData && dsf.type(this.currentData) == "array" ? this.currentData : null) || [];
  },
  methods: {
    getRowId(row) {
      let key = row.id;
      return key;
    },
    getParentRow(row) {
      let r = _.find(this.tableData, (r) => r.id == row.parent);
      return r || {};
    },
    toList(node, data) {
      if (data.field) {
        let tableData=this.rules[this.currentRule]
        let index = _.findIndex(tableData, (row) => {
          return row.id == data.id;
        });
        if (index < 0) {
          var row = {
            id: data.id,
            table: data.id.substr(0, data.id.indexOf(".")),
            code: data.metadata.code,
            valueAttributes: _.map(data.metadata.valueAttributes, "code"),
            currentValueAttribute: data.metadata.valueAttributes.length > 1 ? data.code : data.metadata.valueAttributes[0].code,
            caption: data.fieldCaption,
            format: ""
          };
          tableData.push(row)
        } else {
          dsf.layer.pc.message("已经存在，请勿重复添加", false);
        }
      }
    },
    getMaxRule() {
      let allRules = Object.keys(this.rules);
      let max = 0;
      _.each(allRules, (v) => {
        let v1 = v.replace(/[^\d]/g, "");
        let num = parseInt(v1);
        if (num > max) {
          max = num;
        }
      });
      return max;
    },
    tabAddHandler() {
      let max = this.getMaxRule();
      this.$set(this.rules, "rule-" + (max + 1), []);
      max = this.getMaxRule();
      this.currentRule = "rule-" + max;
    },
    tabRemoveHandler(name) {
      dsf.layer
        .confirm("是否要删除" + name)
        .then(() => {
          this.$delete(this.rules, name);
          let max = this.getMaxRule();
          this.currentRule = "rule-" + max;
        })
        .catch(() => {});
    },
    async getTreeData(metadata, rm_type) {
      let data = [];
      if (rm_type == "8") {
        let params = {
          rmId: metadata.rmId
        };
        data = await this.$webAPI.getDbSourceFieldsTree(params);
      } else if (rm_type == "3") {
        let params = {
          namespace: metadata.at
        };
        data = await this.$webAPI.getMetaDataFieldsTree(params);
      }
      return data;
    },
    remove(row) {
       let tableData=this.rules[this.currentRule]
      dsf.array.remove(tableData, row);
    },
    up(row) {
      let tableData=this.rules[this.currentRule]
      let index = _.findIndex(tableData, (r) => r == row);
      if (index > 0) {
        let targetRow = tableData[index - 1];
        this.$set(tableData, index - 1, row);
        this.$set(tableData, index, targetRow);
      }
    },
    down(row) {
       let tableData=this.rules[this.currentRule]
      let index = _.findIndex(tableData, (r) => r == row);
      if (index < tableData.length - 1) {
        let targetRow =tableData[index + 1];
        this.$set(tableData, index + 1, row);
        this.$set(tableData, index, targetRow);
      }
    },
    yes() {
      let result = this.rules;
      return result;
    }
  }
};
</script>