<template>
  <div style="width:100%;height:100%">
    <div style="padding:5px">
      <DsfButton icon="baocun" btn-style="icon-text">保存</DsfButton>
    </div>
    <el-tabs class="ds-user-dev" v-model="active" type="card" style="width:100%;">
      <el-tab-pane label="模板" name="template" lazy style="height:100%">
        <MonacoEditor ref="vueTemplate" :code="vueTemplate" language="html"></MonacoEditor>
      </el-tab-pane>
      <el-tab-pane label="js代码" name="code" lazy style="height:100%">
        <MonacoEditor ref="extCode" :code="extCode"></MonacoEditor>
      </el-tab-pane>
      <el-tab-pane label="样式" name="css" lazy style="height:100%">
        <MonacoEditor ref="css" :code="css" language="scss"></MonacoEditor>
      </el-tab-pane>
      <el-tab-pane label="设计器属性配置" name="json" lazy style="height:100%">
        <MonacoEditor ref="json" :code="jsonCode" language="json"></MonacoEditor>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import userCode from "../userCode.txt";
export default {
  name: 'DsfUserComponentDev',
  props: {
    vueTemplate: {
      type: String,
      default: "<template>\r\n\t<div></div>\r\n</template>"
    },
    css: {
      type: String,
      default: ""

    },
    extCode: {
      type: String,
      default: userCode
    },
    jsonCode:{
      type: String,
      default: "{}"
    }
  },
  data() {
    return {
      active: "template"
    }
  },
  methods: {
    yes() {
      let extCode = this.$refs.extCode ? this.$refs.extCode.getContent() : this.extCode;
      let css = this.$refs.css ? this.$refs.css.getContent() : this.css;
      let vueTemplate = this.$refs.vueTemplate ? this.$refs.vueTemplate.getContent() : this.vueTemplate;
      return {
        extCode: extCode,
        css: css,
        vueTemplate: vueTemplate,
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.ds-user-dev{
  height:calc(100% - 50px);
}
.ds-user-dev /deep/ {
  & > .el-tabs__content {
    height: calc(100% - 56px);
  }
}
</style>