<template>
  <el-drawer
    title="页面文档"
    :visible.sync="show"
    :append-to-body="true"
    :modal-append-to-body="true"
    :destroy-on-close="false"
    :wrapperClosable="false"
    size="50%"
    custom-class="pagedoc-drawer"
    direction="rtl"
    @close="handleClose">
    <editor-menu-bubble :editor="editor" :keep-in-bounds="true" v-slot="{ commands, isActive }">
      <div class="menububble">
        <!-- <div :class="{ 'menububble-item': true ,'is-active': isActive.component_block() }" @click="commands.component_block">
          <i class="iconfont icon-duohangwenben"></i>
        </div> -->
        <div :class="{ 'menububble-item': true ,'is-active': isActive.bold() }" @click="commands.bold">
          <i class="iconfont icon-bold"></i>
        </div>
        <div :class="{ 'menububble-item': true ,'is-active': isActive.italic() }" @click="commands.italic">
          <i class="iconfont icon-jurassic_font-tilt"></i>
        </div>
        <div :class="{ 'menububble-item': true ,'is-active': isActive.heading({ level: 1 }) }" @click="commands.heading({ level: 1 })">
          <i class="iconfont icon-h-1"></i>
        </div>
        <div :class="{ 'menububble-item': true ,'is-active': isActive.heading({ level: 2 }) }" @click="commands.heading({ level: 2 })">
          <i class="iconfont icon-h-2"></i>
        </div>
        <div :class="{ 'menububble-item': true ,'is-active': isActive.heading({ level: 3 }) }" @click="commands.heading({ level: 3 })">
          <i class="iconfont icon-h-3"></i>
        </div>
        <div :class="{ 'menububble-item': true ,'is-active': isActive.bullet_list() }" @click="commands.bullet_list">
          <i class="iconfont icon-richtextbulletedlist"></i>
        </div>
        <div :class="{ 'menububble-item': true ,'is-active': isActive.ordered_list() }" @click="commands.ordered_list">
          <i class="iconfont icon-youxuliebiao"></i>
        </div>
        <div :class="{ 'menububble-item': true }" @click="commands.align({ align: 'left' })">
          <i class="iconfont icon-zuoduiqi"></i>
        </div>
        <div :class="{ 'menububble-item': true }" @click="commands.align({ align: 'center' })">
          <i class="iconfont icon-juzhongduiqi"></i>
        </div>
        <div :class="{ 'menububble-item': true }" @click="commands.align({ align: 'right' })">
          <i class="iconfont icon-youduiqi"></i>
        </div>
        <div :class="{ 'color-item': true, 'menububble-item': true }" :style="{ borderBottomColor: fontColor }">
          <i class="iconfont icon-T-yanse"></i>
          <el-color-picker @change="commands.colormark({ color: fontColor })" v-model="fontColor"></el-color-picker>
        </div>
        <div :class="{ 'color-item': true, 'menububble-item': true  }" :style="{ borderBottomColor: bgColor }">
          <i class="iconfont icon-ziyuan1"></i>
          <el-color-picker v-model="bgColor" @change="commands.backgroundmark({ color: bgColor })"></el-color-picker>
        </div>
        <div :class="{ 'menububble-item': true  }" @click="commands.horizontal_rule">
          <i class="iconfont icon-758bianjiqi_fengexian"></i>
        </div>
        <div :class="{ 'menububble-item': true }" @click="commands.undo">
          <i class="iconfont icon-shangyibu1"></i>
        </div>
        <div :class="{ 'menububble-item': true }" @click="commands.redo">
          <i class="iconfont icon-xiayibu1"></i>
        </div>
        <div :class="{ 'menububble-item': true }" @click="showSearch = !showSearch">
          <i class="iconfont icon-sousuo"></i>
        </div>
      </div>
    </editor-menu-bubble>
    <div class="search-container" v-show="showSearch">
      <el-input style="width: 200px" v-model="searchText" placeholder="请输入关键字" @keydown.enter.native="search">
        <el-button @click="search" slot="append" icon="el-icon-search"></el-button>
      </el-input>
      <el-input style="width: 350px" v-model="replaceText" placeholder="请输入替换字符" @keydown.enter.native="replace">
        <el-select style="width: 110px" v-model="replaceType" slot="prepend" placeholder="请选择">
          <el-option label="单个替换" value="1"></el-option>
          <el-option label="替换所有" value="2"></el-option>
        </el-select>
        <el-button :disabled="!searchText" @click="replace" slot="append" icon="el-icon-s-fold"></el-button>
      </el-input>
    </div>
    <div :style="pos" class="autoselect-menu" ref="menu" v-show="showRelationControls">
      <div tabindex="1" @click="selectRelatComponent(item)" @keydown.stop="handleMenuKeydown" class="autoselect-menu-item" v-for="item in relationPageControls" :key="item.designId">{{ item.caption }}</div>
    </div>
    <editor-content class="pageeditor" :editor="editor" />
  </el-drawer>
</template>
<script>
import { Editor, EditorContent, EditorMenuBubble, Doc } from "tiptap/dist/tiptap.common";
import { Blockquote, BulletList, CodeBlock, HardBreak, Heading, ListItem, OrderedList, TodoItem, TodoList, Bold, Code, Italic, Link, Strike, Underline, History, HorizontalRule, Search } from "tiptap-extensions";
import MarkTag from "./extensions/markTag";
import Align from "./extensions/align";
import Colors from "./extensions/colors";
import Background from "./extensions/background";
import Paragraph from "./extensions/paragraph";
export default {
  name: "page-doc",
  components: {
    EditorContent,
    EditorMenuBubble
  },
  data() {
    return {
      editor: null,
      relationPageControls: [],
      showRelationControls: false,
      pos: {},
      insertPos: {},
      show: false,
      fontColor: "#efefef",
      bgColor: "transparent",
      showSearch: false,
      replaceType: "1",
      searchText: "",
      replaceText: "",
      isMark: false
    };
  },
  methods: {
    selectRelatComponent(item) {
      this.showRelationControls = false;
      const transaction = this.editor.state.tr.insertText(`%%${item.caption}%%`);
      this.editor.view.dispatch(transaction);
      const reg = new RegExp(`@%%${item.caption}%%`);
      const str = `<span did="${item.designId}" class="marktag">${item.caption}</span>`;
      const content = this.editor.getHTML().replace(reg, str);
      this.editor.setContent(content);
      setTimeout(() => {
        this.editor.focus();
      }, 0);
    },
    handleClose() {
      this.$parent.pageDocument = this.editor.getHTML();
    },
    handleMenuKeydown(e) {
      if (e.which === 38) {
        const prev = $(e.target).prev(".autoselect-menu-item");
        if (prev) {
          prev.focus();
        }
      } else if (e.which === 40) {
        const next = $(e.target).next(".autoselect-menu-item");
        next.focus();
      } else if (e.which === 13) {
        $(e.target).trigger("click");
      }
    },
    getPageControls() {
      const arr = [];
      const recursionFn = (data) => {
        data.forEach((it) => {
          if (it.caption) {
            arr.push(it);
            if (it.slots || it.controls) {
              recursionFn(it.controls || it.slots);
            }
          } else if (it.controls && Array.isArray(it.controls)) {
            recursionFn(it.controls);
          } else if (it.slots && Array.isArray(it.slots)) {
            recursionFn(it.slots);
          }
        });
      };
      recursionFn(this.$parent.page.slots);
      this.relationPageControls = arr;
    },
    toggleTip(e) {
      if (!this.$refs["menu"]?.contains(e.target)) {
        this.showRelationControls = false;
      }
    },
    showComponentDoc({ id }) {
      this.show = true;
      const html = this.$parent.pageDocument || "";
      if (id) {
        this.isMark = true;
        const div = $("<div></div>");
        div.append($(html));
        const target = div.find(`[did=${id}]`);
        const comBlock = target.parents('div');
        if(comBlock.length) {
          comBlock.addClass('mark');
          this.editor.setContent(div.html());
        } else {
          this.editor.setContent(html);
          this.editor.commands.find(div.find(`[did=${id}]`)?.text());
        }
      } else {
        this.editor.setContent(html);
      }
    },
    search() {
      this.editor.commands.find(this.searchText);
      this.showSearch = false;
    },
    replace() {
      if (this.replaceType == 1) {
        this.editor.commands.replace(this.replaceText);
      } else {
        this.editor.commands.replaceAll(this.replaceText);
      }
      this.showSearch = false;
    },
    getDocContent(){

    }
  },
  mounted() {
    const context = this;
    this.editor = new Editor({
      editable: true,
      autoFocus: true,
      useBuiltInExtensions: true,
      extensions: [
        new Blockquote(),
        new BulletList(),
        new CodeBlock(),
        new HardBreak(),
        new Heading({ levels: [1, 2, 3] }),
        new ListItem(),
        new OrderedList(),
        new TodoItem(),
        new TodoList(),
        new Link(),
        new Bold(),
        new Code(),
        new Italic(),
        new Strike(),
        new Underline(),
        new History(),
        new HorizontalRule(),
        new Search({
          disableRegex: false
        }),
        new Align(),
        new Colors(),
        new Background(),
        new MarkTag(),
        new Doc(),
        new Paragraph(),
      ],
      content: "",
      editorProps: {
        handleTextInput: _.throttle(
          function (view, from, to, text) {
            if (text === "@") {
              context.getPageControls();
              context.showRelationControls = true;
              const { state } = view;
              const { selection } = state;
              const { from, to } = selection;
              const el = document.querySelector(".pageeditor");
              const ct = el.offsetTop;
              const estyle = window.getComputedStyle(el);
              const { width, height } = estyle;
              const cl = document.querySelector(".pagedoc-drawer").offsetLeft;
              const frompos = view.coordsAtPos(from);
              let left = frompos.left - cl + 10;
              let top = frompos.top - ct - 27;
              const maxLeft = parseInt(width) - 200 - 10;
              context.pos = {
                left: left > maxLeft ? `${maxLeft}px` : `${left}px`,
                top: `${top}px`
              };
              context.$nextTick(() => {
                let box = document.querySelector(".autoselect-menu-item:first-child");
                box.removeAttribute("tabindex");
                window.setTimeout(() => {
                  box.setAttribute("tabindex", 0);
                  box.focus();
                });
              });
              context.insertPos = { from, to };
            }
            return false;
          },
          500,
          {
            leading: true,
            trailing: false
          }
        )
      },
      onUpdate({ getHTML }) {
        context.$parent.pageDocument = getHTML();
      },
      onFocus() {
        if (context.isMark) {
          const box = $("<div>");
          box.html(context.editor.getHTML());
          box.find("div.mark").removeClass("mark");
          context.editor.setContent(box.html());
          context.isMark = false;
        }
      }
    });
    window.addEventListener("click", this.toggleTip);
    this.$bus.$on("showComponentDoc", this.showComponentDoc);
  },
  beforeDestroy() {
    this.editor?.destroy?.();
    window.removeEventListener("click", this.toggleTip);
    this.$bus.$off("showComponentDoc", this.showComponentDoc);
  }
};
</script>