<template>
  <div>
    <SelectTree :tree-data="flowData" :select.sync="selectFlow" :checked="checked" :show-search="true"></SelectTree>
  </div>
</template>
<script>
export default {
  name: "SetOpinionNode",
  props: {
    value: {
      type: String,
      default() {
        return "";
      },
    },
    subNodes: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      flowData: [],
      selectFlow: null,
      checked: [],
    };
  },
  created() {
    this.initNodes();
  },
  mounted() {},
  methods: {
    yes() {
      return this.selectFlow;
    },
    validate() {
      if (!this.selectFlow) {
        dsf.layer.message("请选择流程", false);
        return false;
      }
      return true;
    },
    // 获取角色树
    initNodes() {
      this.checked = [this.value];
      this.flowData = this.formatData(JSON.parse(JSON.stringify(this.subNodes)));
    },
    formatData(data, disabledVaule) {
      data.forEach((item) => {
        item.label = item.name;
        if (this.value === item.id) {
          this.selectFlow = item;
        }
      });

      return data;
    },
  },
};
</script>
<style lang="scss" scoped></style>
