import { Mark } from "tiptap/dist/tiptap.common"
import { updateMark, removeMark } from "tiptap-commands/dist/commands.common"

export default class MarkTag extends Mark {

  get name() {
    return 'marktag'
  }

  get defaultOptions() {
    return {
      did: null,
      class: ''
    }
  }

  get schema() {
    return {
      attrs: {
        did: {
            default: null,
        },
        class: {
            default: null,
        }
      },
      inclusive: false,
      parseDOM: [
        {
          tag: 'span[did]',
          getAttrs: dom => ({
            did: dom.getAttribute('did'),
            class: dom.getAttribute('class'),              
          }),
        },
      ],
      toDOM: node => {
          return ['span', {
            did: this.options.did,
            class: this.options.class,              
            ...node.attrs,
          }, 0]
      },
    }
  }

  commands({ type }) {
    return attrs => {
      if (attrs.did) {
        return updateMark(type, attrs)
      }

      return removeMark(type)
    }
  }

}
