<template>
  <div class="des-custom-list-set-list">
    <el-form :model="formInline" label-width="145px">
      <el-row>
        <el-col :span="12">
          <el-form-item label="列表高亮字段：">
            <el-input
              v-model="formInline.lightCode"
              placeholder="字段值为true时高亮(支持表达式)"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="跳转链接字段：">
            <el-input
              v-model="formInline.linkCode"
              placeholder="字段有值时跳转(支持表达式)"
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="跳转链接打开类型：">
            <el-input
              v-model="formInline.linkType"
              placeholder="true:新窗口 false:弹框 (支持表达式)"
            ></el-input> </el-form-item
          ></el-col>
        <el-col :span="12">
          <el-form-item label="查看更多数据链接：">
            <el-input
              v-model="formInline.moreUrl"
              placeholder="设置后显示更多(支持表达式)"
            ></el-input> </el-form-item
          ></el-col>
      </el-row>
    </el-form>
    <div style=" text-align: right;margin-bottom: 10px;">
      <DsfButton size="small" icon="hao" @click="handleEvent('add')">新增</DsfButton>
      <DsfButton size="small" icon="iconfont icon-delete" type="plain" @click="handleEvent('delete')">清空</DsfButton>
    </div>
    <el-table :data="formInline.list" border style="width: 100%" :header-cell-style="{backgroundColor:'#f2f2f2'}"  >
      <el-table-column type="index" label="序号" width="50px" align="center"></el-table-column>
      <el-table-column prop="des" label="字段描述">
        <template v-slot:default="scope">
          <el-input v-model="scope.row.des" />
        </template>
      </el-table-column>
      <el-table-column prop="code" label="对应字段">
        <template v-slot:default="scope">
          <el-input v-model="scope.row.code"/> 
        </template>
      </el-table-column>
      <el-table-column prop="title" label="显示标题">
        <template v-slot:default="scope">
          <el-input v-model="scope.row.title" />
        </template>
      </el-table-column>
      <el-table-column prop="text" label="数据格式">
        <template v-slot:default="scope">
          <el-select v-model="scope.row.text">
            <el-option label="居左" value="left"></el-option>
            <el-option label="居中" value="center"></el-option>
            <el-option label="居右" value="right"></el-option>
          </el-select>
        </template>
      </el-table-column>
      <el-table-column prop="width" label="宽度">
        <template v-slot:default="scope">
          <el-input v-model="scope.row.width" />
        </template>
      </el-table-column>
      <el-table-column prop="ellipsis" label="一行显示">
        <template v-slot:default="scope">
          <el-select v-model="scope.row.ellipsis">
            <el-option label="否" value="0"></el-option>
            <el-option label="是" value="1"></el-option>
          </el-select>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template v-slot:default="scope">
          <el-button type="text" @click="handleEvent('sort', scope, 'up')" v-if="scope.$index>0">上移</el-button>
          <el-button type="text" @click="handleEvent('sort', scope, 'down')" v-if="scope.$index < formInline.list.length-1">下移</el-button>
          <el-button type="text" @click="handleEvent('delete',scope)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";
export default {
  name: "DesCustomListSetList",
  mixins: [propertyDialogSetting],
  data() {
    return {
      formInline: {
        linkType: null,
        linkCode: null,
        moreUrl: "",
        lightCode: null,
        list: [
          {
            des: "类型",
            width: "25%",
            text: "left",
            ellipsis: "1",
            code: null,
            title: ''
          },
          {
            des: "标题、描述",
            width: "25%",
            text: "left",
            ellipsis: "1",
            code: null,
            title: ''
          },
          {
            des: "来源人/物、目的人/物",
            width: "25%",
            text: "left",
            ellipsis: "1",
            code: null,
            title: ''
          },
          {
            des: "时间",
            width: "25%",
            text: "right",
            ellipsis: "1",
            code: null,
            title: ''
          },
        ],
      },
    };
  },
  created() {
    if( this.owner.formInline.list) {
      this.formInline = dsf.mix(true, {}, this.owner.formInline);
    }
  },
  methods: {
    handleEvent(type, item, key) {
      const handler = {
        add: () => {
          let list =  dsf.mix(true, {}, {
						des: '',
						width: '100%',
						text: 'left',
						ellipsis: '1',
            code: null,
            title: ''
					});
          this.formInline.list.push(list);
        },
        sort: () => {
          let index = item.$index
          if (key == "up") {
            var prev = this.formInline.list[index - 1];
            this.formInline.list.splice(index - 1, 1);
            this.formInline.list.splice(index, 0, prev);
          } else {
            var next = this.formInline.list[index + 1];
            this.formInline.list.splice(index + 1, 1);
            this.formInline.list.splice(index, 0, next);
          }
        },
        delete: () => {
          if(item){
            this.formInline.list.splice(item.$index, 1);
          }else {
            this.formInline.list = []
          }
        },
      };
      handler[type] && handler[type]();
    },
    validate() {
      for(let i = 0, l = this.formInline.list.length; i < l; i++) {
        let item = this.formInline.list[i];
        if(dsf.isUnDef(item.code) || !item.title){
          dsf.layer.message("新增表格字段不能为空", false);
          return false;
        }
      }
      return true
    },
    yes() {
      return this.formInline;
    },
  },
};
</script>
