<template>
  <el-container v-loading="loading" class="ds-designer-mobile-setting">
    <el-aside width="300px">
      <DsfPanel>
        <template v-slot:title>配置</template>
        <template v-slot:default>
          <el-tree ref="tree" :data="treeData" node-key="type" :expand-on-click-node="false" :current-node-key="currentType" default-expand-all highlight-current @current-change="selectedNode"></el-tree>
        </template>
      </DsfPanel>
    </el-aside>
    <el-main>
      <div class="form">
        <DsfPanel>
          <template v-slot:title>{{ title }}</template>
          <template v-slot:header>
            <DsfButton icon="baocun" @click="save()">保存</DsfButton>
            <DsfButton icon="shuaxin1" type="plain" @click="refresh()">刷新</DsfButton>
          </template>
          <template>
            <!--基本信息-->
            <el-form v-if="currentType=='info'&&info">
              <el-form-item label="AppID">
                <el-input v-model="info['id']" disabled></el-input>
              </el-form-item>
              <el-form-item label="AppCode">
                <el-input v-model="info['code']" disabled></el-input>
              </el-form-item>
              <el-form-item label="名称">
                <el-input v-model="info['name']" disabled></el-input>
              </el-form-item>
            </el-form>
            <!--导航配置-->
            <el-form v-if="currentType=='navs'&&navs" :label-position="'right'" size="small">
              <div style="margin-bottom:10px;text-align:right">
                <DsfButton icon="hao" type="plain" size="small" @click="addNav()">新增</DsfButton>
              </div>
              <el-card v-for="(nav,index) in navs" :key="index" style="margin-bottom:10px">
                <template>
                  <div slot="header">
                    <div style="float: right;">
                      <el-button style="padding: 3px 0" type="text" @click="remove(navs,nav)">删除</el-button>
                      <el-button style="padding: 3px 0" type="text" @click="up(navs,nav)">上移</el-button>
                      <el-button style="padding: 3px 0" type="text" @click="down(navs,nav)">下移</el-button>
                    </div>
                    <div style="clear:both"></div>
                  </div>
                </template>
                <template>
                  <el-row :gutter="20">
                    <el-col :span="12">
                      <el-form-item label="名称">
                        <el-input v-model="nav.text"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="12">
                      <el-form-item label="地址">
                        <el-input v-model="nav.href"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="24">
                      <el-form-item label="图标类型">
                        <el-radio-group v-model="nav.iconType">
                          <el-radio label="icon">icon</el-radio>
                          <el-radio label="image">图片</el-radio>
                        </el-radio-group>
                      </el-form-item>
                    </el-col>
                    <template v-if="nav.iconType=='image'">
                      <el-col :span="12">
                        <el-form-item label="图片地址">
                          <el-input v-model="nav.imagePath"></el-input>
                        </el-form-item>
                      </el-col>
                      <el-col :span="12">
                        <el-form-item label="选中图片地址">
                          <el-input v-model="nav.activeImagePath"></el-input>
                        </el-form-item>
                      </el-col>
                      <el-col :span="24">
                        <el-form-item label="大图标">
                          <el-radio-group v-model="nav.big">
                            <el-radio :label="true">是</el-radio>
                            <el-radio :label="false">否</el-radio>
                          </el-radio-group>
                        </el-form-item>
                      </el-col>
                    </template>
                    <template v-else>
                      <el-col :span="12">
                        <el-form-item label="图标">
                          <el-input v-model="nav.icon"></el-input>
                        </el-form-item>
                      </el-col>
                      <el-col :span="12">
                        <el-form-item label="选中图标">
                          <el-input v-model="nav.activeIcon"></el-input>
                        </el-form-item>
                      </el-col>
                    </template>
                  </el-row>
                </template>
              </el-card>
            </el-form>
            <!--预加载配置-->
            <el-form v-if="currentType=='preloads'&&preloads" :label-position="'right'" size="small">
              <div style="margin-bottom:10px;text-align:right">
                <DsfButton icon="hao" type="plain" size="small" @click="addPreLoad()">新增</DsfButton>
              </div>
              <el-card v-for="(pre,index) in preloads" :key="index" style="margin-bottom:10px">
                <template>
                  <div slot="header">
                    <div style="float: right;">
                      <el-button style="padding: 3px 0" type="text" @click="remove(pre,preloads)">删除</el-button>
                    </div>
                    <div style="clear:both"></div>
                  </div>
                </template>
                <template>
                  <el-row :gutter="20">
                    <el-col :span="24">
                      <el-form-item label="地址">
                        <el-input v-model="pre.href"></el-input>
                      </el-form-item>
                    </el-col>
                  </el-row>
                </template>
              </el-card>
            </el-form>
          </template>
        </DsfPanel>
      </div>
      <div class="demo">
        <div v-loading="iframeLoading" class="ds-designer-phone">
          <DsfDemoBrowserNavBar class="browser-nav-bar" :show-back="demo.showBack" :title="demo.title" :go-back="goBack"></DsfDemoBrowserNavBar>
          <iframe ref="appDemoFrame" scrolling="no" name="appDemo" :src="src"></iframe>
        </div>
      </div>
    </el-main>
  </el-container>
</template>
<script>
export default {
  name: "MobileSetting",
  data() {
    return {
      loading: false,
      iframeLoading: false,
      appId: "",
      currentType: "info",
      treeData: [
        {
          label: "app配置",
          type: 'root',
          children: []
        }
      ],
      title: "",
      navs: null,
      info: null,
      preloads: null,
      src: "",
      // defaultCurrentKey: "info",
      demo: {
        showBack: false,
        title: "",
        route: null
      }
    }
  },
  watch: {
    'demo.route': {
      handler(v) {
        if (v) {
          this.demo.showBack = v?.meta?.level > 1 ? true : false;
          this.demo.title = v?.meta?.title || "无标题";
        }
      },
      immediate: true
    }
  },
  beforeCreate() {
    window.$mobileSetting = this;
  },
  created() {
    let data = null;
    this.appId = this.$route.query.id;
    this.loadData()
      .then((data) => {
        !data && (data = this.getDefaultConfig());
        this.navs = data.navs || [];
        this.info = data.info;
        this.preloads = data.preloads || [];
        let treeNode = [
          { label: "基本配置", type: "info", data: this.info },
          { label: "导航配置", type: "navs", data: this.navs },
          { label: "预加载", type: "preloads", data: this.preloads }
        ]
        window.sessionStorage.setItem("appId", this.info.code);
        this.treeData[0].children = treeNode;
        this.selectedNode(this.treeData[0].children[0]);
        this.loadIframe();
      });
  },
  mounted() {

  },
  beforeDestroy() {
    delete window.$mobileSetting;
    window.sessionStorage.removeItem("appId")
  },
  methods: {
    loadData() {
      let id = this.$route.query.id
      let params = {
        pageName: "mobileappedit",
        namespace: "dsfa.mobile",
        data: JSON.stringify({
          _id: id
        })
      };
      return this.$webAPI.getFormData(params)
        .then(({ data }) => {
          let info = {
            'id': data.data['dsfa_rm.dsfa_rm_id'],
            'code': data.data['dsfa_rm.ID'],
            'name': data.data['dsfa_rm.name']
          }
          let navs = [], preloads = [];
          let content = data.data['dsfa_rm.content'] ? JSON.parse(data.data['dsfa_rm.content']) : { navs: [], preloads: [] };
          content.info = info;
          return content;
        })
    },
    selectedNode(data) {
      if (data.type == 'root') {
        return;
      }
      this.title = data.label;
      this.currentType = data.type;
    },
    getDefaultConfig() {
      let app = {
        "info": {
          id: "",
          name: "",
          code: ""
        },
        "navs": [],
        "preloads": []
      }
      return app
    },
    loadIframe() {
      let navs = this.treeData[0].children[1].data || [];
      let url = navs[0] ? navs[0].href : "";
      url = dsf.url.anchorToRoutePath(url);
      if (url) {
        this.src = "mobile-index.html?#" + url;
      }
      else {
        this.src = ""
      }
    },
    save() {
      // debugger;
      _.each(this.navs || [], (el) => {
        el.href = dsf.url.anchorToRoutePath(el.href);
        el.href = this.$replace(el.href);
      })
      _.each(this.preloads || [], (el) => {
        el.href = dsf.url.anchorToRoutePath(el.href);
        el.href = this.$replace(el.href);
      })
      let data = {
        'dsfa_rm.content': JSON.stringify({
          navs: this.navs,
          preloads: this.preloads
        })
      }
      let params = {
        namespace: "dsfa.mobile",
        pageName: "mobileappedit",
        data: JSON.stringify({
          '_id': this.$route.query.id,
          ...data
        })
      }
      this.loading = true;
      this.$webAPI.formSave(params)
        .then(({ data }) => {
          if (data.state == '20000') {
            dsf.layer.message("保存成功");
          }
          else {
            dsf.layer.message(data.message, false);
          }
        })
        .catch((err) => {
          dsf.layer.message("保存失败", false);
        })
        .finally(() => {
          this.loading = false;
          this.refresh();
        })
    },
    refresh() {
      this.iframeLoading = true;
      this.src = "";
      this.$nextTick(() => {
        window.setTimeout(() => {
          this.loadIframe()
          this.iframeLoading = false;
        }, 500)
      })

    },
    getDefaultInfo() {
      return {
        name: ""
      }
    },
    getDefaultNavButton() {
      return {
        text: "",
        href: "",
        iconType: "icon",
        icon: "",
        activeIcon: "",
        imagePath: "",
        activeImagePath: ""
      }
    },
    getDefaultPreLoad() {
      return {
        href: ""
      }
    },
    addPreLoad() {
      if (this.preloads) {
        let pre = this.getDefaultPreLoad();
        this.preloads.push(pre);
      }
    },
    addNav() {
      if (this.navs) {
        let nav = this.getDefaultNavButton();
        this.navs.push(nav);
      }
    },
    goBack() {
      let win = this.$refs.appDemoFrame.contentWindow;
      if (win) {
        win.history.back()
      }
    },
    routeChange(to) {
      this.demo.route = to;
    },
    remove(arr, el) {
      dsf.array.remove(arr, el)
    },
    up(arr, el) {
      let index = _.findIndex(arr, el);
      if (index > 0) {
        let target = arr[index - 1];
        this.$set(arr, index - 1, el);
        this.$set(arr, index, target)
      }
    },
    down(arr, el) {
      let index = _.findIndex(arr, el);
      if (index < arr.length - 1) {
        let target = arr[index + 1];
        this.$set(arr, index + 1, el);
        this.$set(arr, index, target)
      }
    }
  }
}
</script>