<template>
  <el-container style="height: 100%">
    <el-aside style="width: 500px; overflow: auto">
      <el-container>
        <el-header
          style="padding: 0px 0px 10px 0px; text-align: right; height: auto"
        >
          <DsfButton size="small" icon="hao" @click="addTab">
            新增标签
          </DsfButton>
        </el-header>
        <el-main style="padding: 0px; height: 100%">
          <el-table
            border
            :show-header="true"
            :data="tableData"
            :highlight-current-row="true"
            @row-click="rowClickHandler"
          >
            <el-table-column
              label="#"
              type="index"
              align="center"
            ></el-table-column>
            <el-table-column label="KEY" prop="text">
              <template v-slot:default="scope">
                {{ scope.row.key }}
              </template>
            </el-table-column>
            <el-table-column label="名称" prop="caption">
              <template v-slot:default="scope">
                {{ scope.row.name }}
              </template>
            </el-table-column>
            <el-table-column label="不显示" prop="text">
              <template v-slot:default="scope">
                {{ scope.row.hide ? "是" : "否" }}
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template v-slot:default="scope">
                <DsfButton
                  btn-style="icon-text"
                  icon="shanchu"
                  size="small"
                  title="删除"
                  @click="removeTab(scope.row)"
                ></DsfButton>
                <DsfButton
                  btn-style="icon-text"
                  icon="shang"
                  size="small"
                  title="上移"
                  @click="up(scope.row)"
                ></DsfButton>
                <DsfButton
                  btn-style="icon-text"
                  icon="xia"
                  size="small"
                  title="下移"
                  @click="down(scope.row)"
                ></DsfButton>
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-aside>
    <el-main style="padding: 0px">
      <el-container>
        <el-main>
          <el-form
            :model="formModel"
            :disabled="!Object.keys(formModel).length"
            label-width="120px"
          >
            <el-form-item label="KEY">
              <el-input v-model="formModel.key"></el-input>
            </el-form-item>
            <el-form-item label="名称">
              <el-input v-model="formModel.name"></el-input>
            </el-form-item>
            <el-form-item label="数据接口">
              <el-input v-model="formModel.dataUrl"></el-input>
            </el-form-item>
            <el-form-item label="跳转SRC">
              <el-input v-model="formModel.iframeSrc"></el-input>
            </el-form-item>
            <el-form-item label="不显示">
              <el-radio-group v-model="formModel.hide">
                <el-radio :label="true">是</el-radio>
                <el-radio :label="false">否</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="使用自定义插槽">
              <el-radio-group v-model="formModel.customize">
                <el-radio :label="true">是</el-radio>
                <el-radio :label="false">否</el-radio>
              </el-radio-group>
            </el-form-item>
          </el-form>
        </el-main>
      </el-container>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";

export default {
  name: "TabsListSetting",
  mixins: [propertyDialogSetting],
  props: {
    defaultShowText: {
      type: Boolean,
      default: true,
    },
    defaultButtonStyle: {
      type: String,
      default: "",
    },
    isDblClickBind: {
      type: Boolean,
      default: false,
    },
    isMobile: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      tableData: [{}],
      formModel: {},
    };
  },
  created() {
    console.log(this.currentData);
    let data = dsf.mix(true, [], this.currentData);
    _.each(data, (el) => {
      if (dsf.isUnDef(el.id)) {
        el.id = dsf.uuid();
      }
    });
    this.tableData = data;
  },
  computed: {
    setRequestUrl() {
      return dsf.url.getWebPath("file/upload?db=1");
    },
  },
  mounted() {},
  methods: {
    rowClickHandler(tab) {
      this.formModel = tab;
    },
    addTab() {
      let { tableData } = this;
      let index = tableData.length + 1;
      this.tableData.push({
        key: `key${index}`,
        name: `name${index}`,
        hide: false,
        dataUrl: "",
        noSearch: false,
        customize: false,
      });
    },
    removeTab(tab) {
      dsf.array.remove(this.tableData, tab);
    },
    up(tab) {
      let index = _.findIndex(this.tableData, (b) => b == tab);
      if (index > 0) {
        let targetTba = this.tableData[index - 1];
        this.$set(this.tableData, index - 1, tab);
        this.$set(this.tableData, index, targetTba);
      }
    },
    down(tab) {
      let index = _.findIndex(this.tableData, (b) => b == tab);
      if (index > 0) {
        let targetTba = this.tableData[index + 1];
        this.$set(this.tableData, index + 1, tab);
        this.$set(this.tableData, index, targetTba);
      }
    },
    validate() {
      let { tableData } = this;
      let state = tableData.some((tab) => {
        let index = 0;
        tableData.map((tab1) => {
          if (tab1.key == tab.key) {
            index++;
          }
        });
        if (index > 1) {
          dsf.layer.message(`请解决重复${tab.key}标识`, false);
          return true;
        }
      });
      if (state) return false;
      return true;
    },
    yes() {
      return this.tableData;
    },
  },
};
</script>
