<template>
  <div ref="designerPanel" v-if="jquuiLoaded" v-loading.fullscreen.lock="loading" class="ds-designer" :class="{ mobile: mobile }">
    <div class="designer-head">
      <div class="designer_tools">
        <DsfDesignerButtons :designer="_self" :show-button="showButton" :buttons="(tpl?templateButtons:buttons)"></DsfDesignerButtons>
      </div>
    </div>
    <div class="designer-content">
      <div class="designer-center">
        <div class="designer-page designer-mobile-wrap">
          <DsfLayoutTree
            v-if="page"
            ref="viewProxy"
            style="position: absolute; height: 100%; width: 100%"
            :is-design="true"
            :component-props.sync="page"
            @dragstart.native.prevent
            @mousedown.native.stop="mouseDownHanlder($event)"
            @mousemove.native="mouseMoveHanlder($event)"
            @contextmenu.native="contextMenuOpen($event)"
            @click.native="selectedComponent($event)"></DsfLayoutTree>
        </div>
        <div class="designer-left">
          <div class="designer-left-tools">
            <el-tabs v-model="layout.toolsBox.activeName">
              <el-tab-pane v-for="tab in layout.toolsBox.tabs" :key="tab.name" :label="tab.label" :name="tab.name">
                <template v-slot:label>
                  <div style="padding-left: 4px">{{ tab.label }}</div>
                </template>
                <div style="padding: 5px">
                  <form action="" @submit.stop.prevent="leftSearch">
                    <el-input v-model="leftSearchInput" class="design-search-input" style="height: 30px; line-height: 30px">
                      <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                  </form>
                </div>
                <!--控件组-->
                <template v-if="tab.name == 'controls'">
                  <el-collapse v-for="(group, $index) in useControlsGroup.groups" :key="$index" v-model="tools.activeName" accordion>
                    <el-collapse-item :name="group.title">
                      <template slot="title">
                        <div :title="group.title" class="ellipsis" style="font-size: 14px">
                          {{ group.title }}
                        </div>
                        <DsfIcon :name="'guanbi2'" style="position: absolute; right: 30px" @click.stop="removeControlGroup(group)"></DsfIcon>
                      </template>
                      <div class="ds-tools-group-content" style="padding: 10px; max-height: 60vh; overflow: auto">
                        <drag-item v-for="(item, itemindex) in group.items" v-show="isMatchLeftSearch(item.name)" :key="itemindex" :data="item"></drag-item>
                      </div>
                    </el-collapse-item>
                  </el-collapse>
                </template>
                <template v-else-if="tab.name == 'metadata'">
                  <!--元数据组-->
                  <el-collapse v-for="(group, $index) in useMetaGroup.groups" :key="$index" v-model="useMetaGroup.activeName" accordion>
                    <el-collapse-item :name="group.id">
                      <template slot="title">
                        <div :title="group.title || group.name" class="ellipsis" style="font-size: 14px">
                          {{ group.title || group.name }}
                        </div>
                        <DsfIcon :name="'guanbi2'" style="position: absolute; right: 30px" @click.stop="removeMetaDataGroup(group)"></DsfIcon>
                      </template>
                      <div class="ds-tools-group-content" style="padding: 10px; max-height: 60vh; overflow: auto">
                        <drag-item v-for="(item, itemindex) in group.items" v-show="isMatchLeftSearch(item.name)" :key="itemindex" :data="item"></drag-item>
                      </div>
                    </el-collapse-item>
                  </el-collapse>
                </template>
                <template v-else-if="tab.name == 'business'">
                  <el-collapse v-for="(group, $index) in useBusinessControlGroup.groups" :key="$index" v-model="tools.activeName" accordion>
                    <el-collapse-item :name="group.title">
                      <template slot="title">
                        <div :title="group.title" class="ellipsis" style="font-size: 14px">
                          {{ group.title }}
                        </div>
                        <DsfIcon :name="'guanbi2'" style="position: absolute; right: 30px" @click.stop="removeBusinessGroup(group)"></DsfIcon>
                      </template>
                      <div class="ds-tools-group-content" style="padding: 10px; max-height: 60vh; overflow: auto">
                        <drag-item v-for="(item, itemindex) in group.items" v-show="isMatchLeftSearch(item.name)" :key="itemindex" :data="item"></drag-item>
                      </div>
                    </el-collapse-item>
                  </el-collapse>
                </template>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <div class="designer-right">
          <DsfDesignerProperties :data="attributesGroup" :owner="attributesOwner"></DsfDesignerProperties>
        </div>
      </div>
    </div>
    <div v-show="dragProxy.show" ref="dragProxy" class="drag-proxy"></div>
    <div v-if="contextMenu.show" ref="contextmenu" class="ds-contextmenu" :style="{ top: contextMenu.pos.y + 'px', left: contextMenu.pos.x + 'px' }">
      <dl class="ds-contextmenu-child ds-contextmenu-anim">
        <template v-for="(item, $index) in contextMenu.items">
          <dd v-if="dsf.type(item) == 'object'" :key="$index" :class="{ disabled: item.disabled }" @click="contextMenuItemClick(item, $event)">
            <a>{{ item.text }}</a>
          </dd>
          <dd v-else-if="item == '-'" :key="$index" class="split"></dd>
        </template>
      </dl>
    </div>
    <page-doc />
  </div>
</template>
<script>
import extDefaultCode from "../extCode.txt";
import dragItem from "./dragItem";
import pageDoc from "./pageEditor/pageDoc";
import control from "../../../mixins/control";
import { jqui } from "_platform/pc/common/importJqueryUI";
import DsfDesignerButtons from "./dsfDesignerButtons";
let businessCode = 12;
let controlCode = 2;
export default {
  name: "DsfDesigner",
  components: { DsfDesignerButtons, dragItem, pageDoc },
  provide() {
    return {
      $designer: this
    };
  },
  props: {
    business: {
      type: String,
      default: ""
    },
    module: {
      type: String,
      default: ""
    },
    pageName: {
      type: String,
      default: ""
    },
    pageTitle: {
      type: String,
      default: ""
    },
    mobile: {
      type: Boolean,
      default: false
    },
    tpl: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      jquuiLoaded: false,
      loading: true,
      firstOpen: true,
      saved: false,
      mousedown: {
        target: null
      },
      design: true,
      buttons: [
        {
          text: "保存",
          id: "save",
          icon: "baocun",
          handler: this.saveLayout,
          setting: true,
          roles: []
        },
        {
          text: "暂存",
          id: "stagesave",
          icon: "baocun",
          handler: this.stageSaveLayout,
          setting: false,
          roles: []
        },
        {
          text: "预览",
          id: "preview",
          icon: "icon_yulan",
          handler: this.preView,
          setting: false,
          roles: []
        },
        {
          text: "模板选择",
          id: "tpl",
          icon: "shebeikaifa1",
          handler: this.useTpl,
          setting: true,
          roles: []
        },
        {
          text: "控件组选择",
          id: "ctrl",
          icon: "icon_shezhi",
          handler: this.selectComponents,
          setting: true,
          roles: []
        },
        {
          text: "元数据选择",
          id: "meta",
          icon: "icon_shezhi",
          handler: this.selectMateGroup,
          setting: true,
          roles: []
        },
        {
          text: "保存业务组件",
          id: "bctrl",
          icon: "biaodanzujian-xialakuang",
          handler: this.saveBusinessComponent,
          setting: true,
          saved: true,
          roles: []
        },
        {
          text: "二次开发",
          id: "js",
          icon: "shebeikaifa1",
          // handler: this.openExtJs,
          handler: this.openDevelopment,
          setting: false,
          roles: [],
          saved: true
        },
        {
          text: "页面文档",
          id: "desc",
          icon: "DOC",
          handler: this.openPageDoc,
          setting: true,
          roles: []
        }
      ],
      //模板设计时显示的按钮
      templateButtons: [
        {
          text: "保存模板",
          icon: "baocun",
          handler: this.saveLayoutToTemplate,
          setting: false,
          roles: [],
          show: () => true
        },
        {
          text: "预览",
          icon: "icon_yulan",
          handler: this.preView,
          setting: false,
          roles: [],
          show: () => true
        },
        {
          text: "模板选择",
          icon: "shebeikaifa1",
          handler: this.useTpl,
          setting: true,
          roles: [],
          show: () => true
        },
        {
          text: "控件组选择",
          icon: "icon_shezhi",
          handler: this.selectComponents,
          setting: true,
          roles: [],
          show: () => true
        },
        {
          text: "功能授权",
          icon: "jiaohuan",
          handler: this.settingFunction,
          setting: false,
          roles: [],
          show: () => true
        }
      ],
      layout: {
        toolsBox: {
          //tab默认选中
          activeName: "controls",
          tabs: [
            { name: "controls", label: "基础组件" },
            { name: "business", label: "业务组件" },
            { name: "metadata", label: "元数据" }
          ]
        }
      },
      page: null,
      tools: {
        activeName: "",
        groups: []
      },
      useControlsGroup: {
        activeName: "",
        groups: []
      },
      useMetaGroup: {
        activeName: "",
        groups: []
      },
      useBusinessControlGroup: {
        activeName: "",
        groups: []
      },
      dragProxy: {
        show: false
      },
      attributesGroup: [],
      attributesOwner: {},
      subTree: [],
      controls: {},
      dragMouseMoveTarget: null,
      contextMenu: {
        show: true,
        items: [],
        pos: { x: -10, y: -10 },
        target: null
      },
      extJsCode: extDefaultCode,
      extCssCode: "",
      extScssCode: "",
      fromTemplateId: "",
      desButtons: {},
      currentUserRoles: {},
      buttonLoad: false,
      leftSearchStr: "",
      leftSearchInput: "",
      cacheFlow: {}, // 缓存当前页面的流程节点,
      pageDocument: "" //在线页面描述文档内容
    };
  },
  computed: {
    dragProxy_el() {
      return this.$refs.dragProxy;
    }
  },
  created() {
    // window.designer = this;
  },
  async mounted() {
    jqui.then(async () => {
      this.jquuiLoaded = true;
      this.loading = true;
      this.$nextTick(async () => {
        try {
          this.init();
          this.currentUserRoles = await this.getCurrentUserRoles();
          await this.loadLayout();
          await this.getFunctionSetting();
        } catch (err) {
          dsf.httpError(err, { message: "设计器加载失败" });
        } finally {
          this.loading = false;
          this.buttonLoad = true;
        }
      });
    });
  },
  updated() {},
  methods: {
    //判断按钮是否显示
    showButton(button) {
      let result = true;
      if (button.saved && !this.saved) {
        return false;
      }
      if (!button.setting) {
        return true;
      }
      if (this.currentUserRoles["SUPER_ADMIN"]) {
        return true;
      } else {
        if (this.fromTemplateId) {
          let desBtnRoles = this.desButtons[button.id];
          if (!desBtnRoles) {
            return false;
          } else {
            let isExist = false;
            _.every(desBtnRoles, (v) => {
              isExist = this.hasRole(v);
              return isExist ? false : true;
            });
            return isExist;
          }
        }
        return true;
      }
    },
    //判断是否存在指定的权限
    hasRole(roleName) {
      let role = this.currentUserRoles[roleName];
      if (role) {
        return true;
      }
      return false;
    },
    dragEnd() {
      dragEnd.call(this);
    },
    init() {
      this.initToolDrag();
      this.initDrop();
    },
    //功能授权管理
    settingFunction() {
      let _this = this;
      this.$openDialog({
        title: "功能授权",
        content: "DesFunctionSetting",
        width: "1000px",
        height: "60vh",
        params: {
          buttons: _.filter(this.buttons, (btn) => btn.setting)
        },
        btns: [
          {
            text: "确定",
            handler(view) {
              let result = view.yes();
              if (result) {
                _.each(result, (button) => {
                  let btn = _.filter(_this.buttons, (b) => b.id == button.id);
                  btn = btn.length > 0 ? btn[0] : null;
                  if (btn) {
                    btn.roles = button.roles;
                  }
                });
              }
            }
          },
          {
            text: "取消"
          }
        ]
      });
    },
    //初始化左侧工具箱拖动
    initToolDrag() {
      initToolDrag.call(this);
    },
    //初始化右侧接受拖动
    initDrop() {
      initDrop.call(this);
    },
    //拖放时候，鼠标经过元素时触发
    dragMouseMoveHandler(evt) {
      let target = $(evt.target);
      let box = null;
      if (target.hasClass("drag-proxy")) {
        return;
      }
      if (target.is("[slot-name]")) {
        let allow = dsf.designer.isAllowDrop(target, this);
        if (allow) {
          box = target;
          this.dragProxy.show = true;
          let proxy = $(this.dragProxy_el);
          box.append(proxy);
        }
      } else {
        let ctrl = target.closest(".designer-ctrl-proxy");
        if (ctrl && ctrl.length > 0) {
          if ((!target.hasClass("designer-mask") && !target.hasClass("ds-control")) || target.hasClass("ds-no-drop")) {
            return;
          }
          let vueComponent = dsf.designer.getComponent(ctrl.get(0).__vue__, this);
          if (vueComponent) {
            if (vueComponent.$$getComponent) {
              vueComponent = vueComponent.$$getComponent();
            }
            box = ctrl.closest("[slot-name]");
            let allow = dsf.designer.isAllowDrop(box, this);
            if (allow) {
              let height = target.height();
              let proxy = $(this.dragProxy_el);
              this.dragProxy.show = true;
              if (evt.offsetY < height / 2) {
                ctrl.before(proxy);
              } else {
                ctrl.after(proxy);
              }
            }
          }
        }
      }
    },
    //拖拽完成
    dropHandler(evt, container, slot, data) {
      dropHandler.call(this, evt, container, slot, data);
    },
    //将组件添加到指定位置
    addControl(parent, slot, child, index) {
      if (!child.designId) {
        child.designId = dsf.uuid();
      }
      if (!child.caption) {
        let arr = child.ctrlType.split(".");
        child.caption = arr[arr.length - 1].toLowerCase() + this.$refs.viewProxy.getSerial();
      }
      if (parent.$$getComponent) {
        //如果是代理组件
        parent = parent.$children[0];
      } else {
        //此处需要有一个判断，组件的根元素直接是elementui的情况
        while (!parent.slots) {
          parent = parent.$parent;
        }
      }
      // if (child.metadata) {
      //   let metadataLevel = 0;
      //   if (isInSubtable(parent)) {
      //     metadataLevel = 1;
      //   }
      // }
      if (parent.$el) {
        //如果在子表中或者列表中是表单组建不显示label区域
        let isSubtableCell = parent.$el.matches(".ds-subtable-cell-content");
        let isDataGridCell = parent.$el.matches(".ds-datagrid-cell-content");
        //如果拖动到子表中
        if (child.isFormControl) {
          if (isSubtableCell) {
            child.showLabel = false;
            child.simple = true;
            child.lrNoPadding = true;
          } else if (isDataGridCell) {
            child.showLabel = false;
            child.simple = false;
            child.lrNoPadding = true;
          } else {
            child.simple = false;
            child.lrNoPadding = false;
          }
        }
      }
      let slotIndex = _.findIndex(parent.slots, (s) => s.name == slot);
      if (slotIndex >= 0) {
        let slotObj = parent.slots[slotIndex];
        if (slotObj && slotObj.textProxy) {
          if (dsf.hasOwn(child, "textProxy")) {
            child.textProxy = true;
            child.showLabel = false;
          }
        }
        //判断是后入还是中间插入
        if (dsf.isUnDef(index) || index < 0) {
          slotObj.controls.push(child);
        } else {
          slotObj.controls.splice(index, 0, child);
        }
        child.isQuestionControl && this.setQuestionIndex(child, slotObj.controls, index);
      }
      // 问卷题自动选择控件
      if (child.isQuestionControl) {
        if (!this.$questionComponentTemp) {
          this.$questionComponentTemp = {};
        }
        setTimeout(() => {
          $(`[des-id=${child.designId}]`).trigger("click");
        }, 0);
      }
      // console.log('模式选择', child);
    },
    //问卷索引设置
    setQuestionIndex(child, childs, postion) {
      // 后入
      if (dsf.isUnDef(postion) || postion < 0) {
        this.setPrevQuestionIndex(child, childs);
      } else {
        let index = 0;
        _.forEach(childs, function (item, i) {
          if (item.isQuestionControl && item.hasQuestionIndex && item.visible) {
            index++;
            item.questionIndex = index;
          }
        });
      }
    },
    //设置childs中child之前所有问卷组件索引
    setPrevQuestionIndex(child, childs) {
      let index = 0;
      _.find(childs, function (item) {
        if (item.isQuestionControl && item.hasQuestionIndex && item.visible) {
          index++;
          item.questionIndex = index;
        }
        if (item.designId == child.designId) {
          return true;
        }
      });
    },
    //设置index之后所有问卷组件索引
    setNextQuestionIndex(index, nexts) {
      _.forEach(nexts, function (item) {
        if (item.hasQuestionIndex && item.visible) {
          item.questionIndex = index;
          index++;
        }
      });
    },
    //选中组件
    selectedComponent($event) {
      window.setTimeout(() => {
        this.contextMenuClose();
        let target = $($event.target);
        let component_el = target.closest(".designer-ctrl-proxy");
        if (component_el.length) {
          let component = component_el.get(0).__vue__;
          let allComponents = $(".designer-ctrl-proxy");
          allComponents.each(function (i, el) {
            let c = el.__vue__;
            c && (c.__desContext__.selected = false);
          });
          if (component) {
            if (!component.isView) {
              component.__desContext__.selected = true;
            }
          }
          this.$http
            .get("$/pd_attributes/" + component.ctrlType + ".json")
            .done((res) => {
              res.forEach((item) =>
                item.attributes.forEach((attr) => {
                  this.format(attr, component);
                })
              );
              this.attributesGroup = res || [];
              this.attributesOwner = component;
              if (component.isQuestionControl && this.$questionComponentTemp && !this.$questionComponentTemp[component.designId]) {
                this.$questionComponentTemp[component.designId] = true;
                setTimeout(() => {
                  this.$bus.$emit(`questionSetting`);
                }, 200);
              }
            })
            .error((err) => {
              this.attributesGroup = [];
              this.attributesOwner = component;
              dsf.error(err);
            });
        }
      }, 0);
      $event.preventDefault();
      $event.stopPropagation();
    },
    format(item, component) {
      // 流程信息--事项名称 单独处理
      if (item.dictionaryControl) {
        if (item.dictionary && item.dictionary.length === 0) {
          item.dictionary = [];
          dsf.recursionComponentProps(component.$$getComponent().$page.$props, (data) => {
            if (data.isFormControl && data.caption !== "wfinfo" && data.ctrlName !== "DsfDisposeIdea" && data.ctrlName !== "DsfMobileDisposeIdea" && data.ctrlName !== "DsfFormCommentBox") {
              if (!("oldFlow" in item)) {
                item.dictionary.push({
                  id: data.designId,
                  text: `${data.label}(${data.caption})`,
                  value: data.caption
                });
              }
            }
            // 单独获取节点
            if (data.caption === "wfinfo" && "oldFlow" in item) {
              if (this.cacheFlow.WFID !== data.chooseFlow.dsfa_rm_id) {
                this.$http
                  .get("wfk/getWFNodeList", {
                    sID: data.chooseFlow.dsfa_rm_id
                  })
                  .then((response) => {
                    if (response.data.success) {
                      response.data.data &&
                        response.data.data.NodeList &&
                        response.data.data.NodeList.forEach((node) => {
                          item.dictionary.push({
                            id: node.NodeID,
                            text: node.NodeName,
                            value: node.NodeID
                          });
                        });
                      this.cacheFlow = response.data.data;
                    }
                  });
              } else {
                this.cacheFlow.NodeList.forEach((node) => {
                  item.dictionary.push({
                    id: node.NodeID,
                    text: node.NodeName,
                    value: node.NodeID
                  });
                });
              }
            }
          });
        }
      }
    },
    /*--- 设计 事件 -----*/
    //处理设计时需要打开右键菜单的组件
    contextMenuOpen(evt) {
      let component = dsf.designer.getComponentByTarget(evt.target);
      if (!component) {
        return;
      }
      dsf.designer.broadcast(this, "clearContextMemuHandler", evt, null, component);
      do {
        let _options = component ? component.$options : {};
        let _contextMenuHandler = _options.design ? _options.design.contextMenuHandler : null;
        if (!_contextMenuHandler) {
          component = component.$parent;
        } else {
          break;
        }
      } while (component != this.$root);
      let options = component.$options;
      let contextMenuHandler = component.$options.design ? component.$options.design.contextMenuHandler : null;
      if (contextMenuHandler) {
        this.contextMenuClose();
        let items = options.design.contextMenuHandler.call(component, evt, this);
        if (items && items.length > 0) {
          this.$nextTick(() => {
            this.contextMenu.items = items;
            this.contextMenu.show = true;
            this.contextMenu.target = component;
            this.$nextTick(() => {
              let cm = this.$refs.contextmenu;
              if (cm) {
                document.getElementById("app").appendChild(cm);
              }
              if (cm && cm.childNodes[0]) {
                cm = cm.childNodes[0];
                // cm.appendChild(document.getElementById("app"))

                this.contextMenu.pos.x = evt.pageX;
                this.contextMenu.pos.y = evt.pageY;
                if (this.contextMenu.pos.y + cm.offsetHeight > this.$refs.designerPanel.offsetHeight) {
                  this.contextMenu.pos.y = evt.pageY - cm.offsetHeight;
                }
              }
            });
          });
          $(document).one("click", this.contextMenuClose);
        }
        evt.stopPropagation();
        evt.preventDefault();
      }
    },
    //右键菜单关闭
    contextMenuClose() {
      this.contextMenu.items = [];
      this.contextMenu.show = false;
      this.contextMenu.target = null;
    },
    //右键菜单点击
    contextMenuItemClick(item, evt) {
      if (!item.disabled) {
        item.handler.call(this.contextMenu.target, item);
      } else {
        evt.preventDefault();
        evt.stopPropagation();
      }
    },
    //处理设计时需要操作鼠标点下事件的组件
    mouseDownHanlder(evt) {
      try {
        if (evt.button != 0) {
          return;
        }
        let component = dsf.designer.getComponentByTarget(evt.target);
        if (!component) {
          return;
        }
        //设计器会向下级所有组件触发,可用于清理下级所有组件触发过mousedown事件留下的痕迹
        dsf.designer.broadcast(this, "clearMouseDownHandler", evt, null, component);
        this.mousedown.target = component;
        let options = component ? component.$options : {};
        if (options.design.mouseDownHandler) {
          options.design.mouseDownHandler.call(component, evt, this);
        }
      } catch (ex) {
        dsf.error(ex);
      } finally {
        $(document).one("mouseup", this.mouseUpHandler);
      }
    },
    //处理设计时需要操作鼠标点下事件的组件,组件必须触发过mousedown
    mouseMoveHanlder(evt) {
      try {
        if (this.mousedown.target) {
          let options = this.mousedown.target.$options || {};
          if (options.design.mouseMoveHandler) {
            options.design.mouseMoveHandler.call(this.mousedown.target, evt, this);
          }
        }
      } catch (ex) {
        dsf.error(ex);
      }
    },
    mouseUpHandler(evt) {
      try {
        if (this.mousedown.target) {
          let options = this.mousedown.target.$options || {};
          if (options.design.mouseUpHandler) {
            options.design.mouseUpHandler.call(this.mousedown.target, evt, this);
          }
        }
      } catch (ex) {
        dsf.error(ex);
      } finally {
        this.mousedown.target = null;
      }
    },
    /*--- 设计 事件 -----*/

    //生成二次开发js
    openDevelopment() {
      let page = this.$refs.viewProxy;
      dsf.designer.createViewMetaData(page, this.mobile);
      this.$nextTick(() => {
        let layout = dsf.designer.getLayoutInfo(page);
        let result = dsf.designer.convertTemplateAndControlData(layout);
        let tpl = dsf.beautify.html(result.tpl);
        dsf.layer.pc.openDialog({
          title: "二次开发",
          fullscreen: true,
          overflow: "hidden",
          content: "Development",
          params: {
            extCode: this.extJsCode || "",
            css: this.extScssCode || "",
            vueTemplate: tpl || ""
          },
          btns: [
            {
              text: "保存并提交",
              handler: (component) => {
                let result = component.yes();
                this.extJsCode = result.extCode;
                this.extScssCode = result.css;
                this.saveLayout();
                return false;
              }
            },
            {
              text: "保存不提交",
              handler: (component) => {
                let result = component.yes();
                this.extJsCode = result.extCode;
                this.extScssCode = result.css;
              }
            },
            {
              text: "关闭"
            }
          ]
        });
      });
    },
    openPageDoc() {
      this.$bus.$emit(`showComponentDoc`, { id: null });
    },
    async loadEmptyDesigner(pageType, pageProps) {
      pageType = pageType || "dsf.page";
      this.page = createComponent(pageType, pageProps, this.mobile);
      //加载控件组
      this.loadControlGrooup.call([]);
    },
    //加载布局
    async loadLayout(business, module, pname) {
      var _this = this;
      let b = business || this.business;
      let m = module || this.module;
      let pn = pname || this.pageName;
      let title = this.pageTitle;
      let ctrlType = this.mobile ? "dsf.mobile.page" : "dsf.page";
      let defaultPageAttrs = {
        caption: "page",
        name: pn,
        title: title,
        context: { B: b, M: m }
      };
      if (b && m && pn) {
        let layout = null,
          scss = null,
          online = null,
          designer = null;
        try {
          let res = await getLayout.call(this, b, m, pn, title, this.mobile, this.tpl);
          let dom = $("<div>" + res.data + "</div>");
          let layoutStr = dom.children("script[id='layout']").html() || "";
          let scssStr = dom.children("script[id='scss']").html() || "";
          let designerStr = dom.children("script[id='designer']").html() || "";
          let onlineStr = dom.children("script[id='onLineComponents']").html() || "";
          let layoutFn = new Function(["var layout=" + (layoutStr || "null") + ";", "return layout;"].join("\n"));
          let scssFn = new Function(["return " + JSON.stringify(scssStr.trim() || "")].join("\n"));
          let designerFn = new Function(["return " + (designerStr || "{}").trim()].join("\n"));
          // let onlineFn = new Function(["return " + (onlineStr || "[]").trim()].join("\n"));
          layout = dsf.fillComponentDefaultProps(layoutFn());
          scss = scssFn();
          designer = designerFn();

          if (!this.tpl) {
            let res1 = await getExtJs.call(this, b, m, pn, this.mobile, this.tpl);
            let code = res1.data;
            this.extJsCode = code;
            let pageDoc = await getPageDocument.call(this, b, m, pn, this.mobile);
            this.pageDocument = pageDoc;
          }
        } catch (err) {
          dsf.error(err);
        } finally {
          if (layout) {
            this.page = createComponent(ctrlType, layout, this.mobile);
            this.extScssCode = scss || "";
            //加载控件组
            let controlGroup = dsf.isEmptyObject(designer.controlGroup) || !designer.controlGroup ? [] : designer.controlGroup;
            this.loadControlGrooup(controlGroup);
            //加载元数据组
            let metadataGroup = designer.metadataGroup || [];
            this.loadMetaDataGroup(metadataGroup);
            //加载业务组件组
            let businessGroup = designer.businessControlGroup || [];
            this.loadBusinessComponetGroup(businessGroup);
            this.fromTemplateId = designer.fromTemplateId || "";
            this.firstOpen = false;
            this.saved = true;
          } else {
            this.loadEmptyDesigner(ctrlType, defaultPageAttrs, []);
            this.fromTemplateId = "";
            this.firstOpen = true;
            this.saved = false;
            //强行弹出模板选择
            if (!this.tpl) {
              this.useTpl(false, false, false);
            }
          }
          // dsf.layer.pc.closeLoading(dialog);
        }
      } else {
        this.loadEmptyDesigner(ctrlType, defaultPageAttrs, []);
        // dsf.layer.pc.closeLoading(dialog);
        this.fromTemplateId = "";
        this.firstOpen = true;
        this.saved = false;
      }
    },
    //加载元数据组
    async loadMetaDataGroup(params) {
      try {
        //去除重复
        params = _.uniqBy(params, (v) => v.id);
        let result = await requestMetaDataGroupContent.call(this, params);
        _.each(result, (r) => {
          let metadataList = r.data;
          metadataList = _.filter(metadataList || [], (m) => {
            if (m.type_value == 3) {
              return _.map(params, "id").indexOf(m.ID) >= 0;
            } else {
              return _.map(params, "id").indexOf(m.dsfa_rm_id) >= 0;
            }
          });
          _.each(metadataList, (group) => {
            metadataToUseToolGroup.call(this, group);
          });
        });
      } catch (ex) {
        dsf.httpError(ex);
      }
    },
    //加载控件组
    async loadControlGrooup(params) {
      //去除重复
      try {
        params = _.uniqBy(params, (v) => v.id);
        let result = await requestControlGroupContent.call(this, params);
        _.each(result, (r) => {
          controlToUseToolGroup.call(this, r);
        });
      } catch (ex) {
        dsf.httpError(ex);
      }
    },
    //加载业务组件租
    async loadBusinessComponetGroup(params) {
      try {
        //去除重复
        params = _.uniqBy(params, (v) => v.id);
        let result = [];
        if (params.length > 0) {
          result = await requestBusinessComponent.call(this, params);
        } else {
          result = [];
        }
        _.each(result.data, (r) => {
          businessToUseToolGroup.call(this, r);
        });
      } catch (ex) {
        dsf.error(ex);
      }
    },
    async getFunctionSetting() {
      let params = {};
      let ns = "";
      if (this.tpl) {
        ns = (this.page.context.B || "") + "." + (this.page.context.M || "") + "." + this.page.name;
      } else {
        ns = this.fromTemplateId;
      }
      if (ns) {
        return await this.setDesButtons(ns);
      }
    },
    //获取当前用户的所有角色
    async getCurrentUserRoles() {
      let roles = await this.$webAPI.getCurrentUserRoles();
      return _.keyBy(roles.data, "serialNumber");
    },
    //根据模板配置获取设计器可用的按钮
    async setDesButtons(ns) {
      let res = await this.$webAPI.getDesignerButtons({ templateId: ns });
      if (res.data) {
        let buttons = res.data.button ? JSON.parse(res.data.button) : [];
        let result = {};
        _.each(buttons, (btn, i) => {
          result[btn.id] = _.map(btn.roles, "serial_number");
          let button = _.find(this.buttons, (el) => el.id == btn.id);
          if (button) {
            button.roles = btn.roles;
          }
        });
        this.desButtons = result;
      }
    },
    //将组件添加到设计器的组件列表中
    pushToControls(component) {
      this.controls[component.designId] = component;
    },
    //保存布局
    saveLayout() {
      let validateResult = dsf.designer.saveLayoutValidate(this.$refs.viewProxy);
      if (validateResult !== false) {
        dsf.designer.saveLayout(this);
      }
    },
    //保存模板
    saveLayoutToTemplate() {
      dsf.designer.saveLayoutToTemplate(this);
    },
    //预览
    preView() {
      preView.call(this);
    },
    //生成业务组件数据
    saveBusinessComponent() {
      let _this = this;
      let controls = [];
      this.$openDialog({
        title: "组件选择",
        width: "1000px",
        height: "50vh",
        params: {
          designer: this
        },
        content: "DesComponents",
        btns: [
          {
            text: "确定",
            handler(data) {
              let vr = data.validate();
              if (vr === false) {
                return false;
              }
              let controlList = data.yes();
              if (controlList.length) {
                _.each(controlList, (el) => {
                  el.props.metadata.id = "";
                  el.props.metadata.at = "";
                  el.props.metadata_fullcode = "";
                  delete el.isFromMetaData;
                });
                window.setTimeout(() => {
                  _this.saveToBusinessComponentGroup(controlList);
                }, 1000);
              }
            }
          },
          { text: "取消" }
        ]
      });
    },
    //保存到业务组件组
    saveToBusinessComponentGroup(controlList) {
      dsf.designer.saveToBusinessComponentGroup(this, controlList);
    },
    // 模板
    useTpl(showClose, showCancel, require) {
      let _this = this;
      let btns = [
        {
          text: "确定",
          handler: async (data) => {
            let tplData = data.radioData;
            if (_.isNull(tplData)) {
              dsf.layer.message("请选择一个模板", false);
              return false;
            } else {
              _this.loading = true;
              var path = tplData.path.split("/");
              var b = path.shift();
              var pn = path.pop();
              var m = path.join(".");
              let layout = null,
                scss = null,
                designer = null;
              try {
                let res = await getLayout.call(this, b, m, pn, this.pageTitle, this.mobile, true);
                let dom = $("<div>" + res.data + "</div>");
                let layoutStr = dom.children("script[id='layout']").html() || "";
                let scssStr = dom.children("script[id='scss']").html() || "";
                let designerStr = dom.children("script[id='designer']").html() || "";
                let layoutFn = new Function(["var layout=" + (layoutStr || "null") + ";", "return layout;"].join("\n"));
                let scssFn = new Function(["return " + JSON.stringify(scssStr.trim() || "")].join("\n"));
                let designerFn = new Function(["return " + (designerStr || "{}").trim()].join("\n"));
                layout = dsf.fillComponentDefaultProps(layoutFn());
                scss = scssFn();
                designer = designerFn();
                // _.each(designer, (v, i) => {
                //   _.each(v, (v1) => {
                //     v1.fromTemplate = true;
                //   });
                // });
                _this.fromTemplateId = tplData.ID;
              } catch (err) {
                dsf.layer.pc.message("读取模板失败", false);
                dsf.error(err);
              } finally {
                if (layout) {
                  await _this.setDesButtons(tplData.ID);
                  let context = _this.page.context;
                  let name = _this.page.name;
                  let title = _this.page.title;
                  _this.page = null;
                  this.$nextTick(() => {
                    //加载控件组
                    let controlGroup = dsf.isEmptyObject(designer.controlGroup) || !designer.controlGroup ? [] : designer.controlGroup;
                    this.loadControlGrooup(controlGroup);
                    //加载元数据组
                    let metadataGroup = designer.metadataGroup || [];
                    this.loadMetaDataGroup(metadataGroup);
                    //加载业务组件组
                    let businessGroup = designer.businessControlGroup || [];
                    this.loadBusinessComponetGroup(businessGroup);
                    //此处dsf.mix是为了保持页面原有命名空间和名称
                    _this.page = createComponent(
                      "dsf.page",
                      dsf.mix(layout, {
                        context: context,
                        name: name,
                        title: title
                      })
                    );
                  });
                }
                _this.loading = false;
              }
            }
          }
        }
      ];
      if (showCancel !== false) {
        btns.push({
          text: "取消"
        });
      }
      dsf.layer.openDialog({
        title: "选择布局模板",
        fullscreen: false,
        content: "DesUseLayoutTpl",
        params: {
          isMobile: this.mobile
        },
        width: "500px",
        height: "500px",
        showClose: showClose !== false,
        btns: btns
      });
    },
    // 自定义组件
    selectComponents() {
      dsf.layer.openDialog({
        title: "选择组件",
        fullscreen: false,
        content: "DesCustomComponent",
        width: "500px",
        btns: [
          {
            text: "确定",
            handler: (data) => {
              const checkNodeList = data.yes();
              // tabsIndex 0 == 控件组（2） 1 == 业务组件(12)
              const typeValue = data.tabIndex == "0" ? controlCode : businessCode;
              if (checkNodeList.length > 0) {
                let ctrlData = checkData.call(this, checkNodeList);
                let list = _.map(ctrlData, (c) => {
                  return {
                    id: c.dsfa_rm_id,
                    type: typeValue
                  };
                });
                typeValue == businessCode ? this.loadBusinessComponetGroup(list) : this.loadControlGrooup(list);
              } else {
                dsf.layer.message("请选择一个组件", false);
                return false;
              }
            }
          },
          {
            text: "取消"
          }
        ]
      });
    },
    // 元数据组
    selectMateGroup() {
      dsf.layer.openDialog({
        title: "选择元数据组",
        fullscreen: false,
        content: "DesMetaDataSelect",
        width: "500px",
        btns: [
          {
            text: "确定",
            handler: (data) => {
              const checkNodeList = data.yes();
              if (checkNodeList.length > 0) {
                let ctrlData = checkData.call(this, checkNodeList);
                let nameSpaceList = [];
                let d = _.map(ctrlData, (data) => {
                  return {
                    id: data.type_value == "8" ? data.dsfa_rm_id : data.ID,
                    type: data.type_value
                  };
                });
                this.loadMetaDataGroup(d);
              }
            }
          },
          {
            text: "取消"
          }
        ]
      });
    },
    removeControlGroup(group) {
      dsf.layer.pc
        .confirm("确定要删除该控件组吗?")
        .then(() => {
          dsf.array.remove(this.useControlsGroup.groups, group);
        })
        .catch(() => {});
    },
    removeMetaDataGroup(group) {
      dsf.layer.pc
        .confirm("确定要删除该元数据吗?")
        .then(() => {
          dsf.array.remove(this.useMetaGroup.groups, group);
        })
        .catch(() => {});
    },
    //删除业务组件组
    removeBusinessGroup(group) {
      dsf.layer.pc
        .confirm("确定要删除该元数据吗?")
        .then(() => {
          dsf.array.remove(this.useBusinessControlGroup.groups, group);
        })
        .catch(() => {});
    },
    leftSearch() {
      this.leftSearchStr = this.leftSearchInput;
    },
    isMatchLeftSearch(name) {
      if (!this.leftSearchStr) {
        return true;
      }
      if (name.indexOf(this.leftSearchStr) >= 0) {
        return true;
      }
      return false;
    }
  }
};

function initToolDrag() {
  //附加工具箱拖动
  $(".ds-tool-item").draggable({
    addClasses: false,
    appendTo: ".ds-designer",
    distance: 10,
    revert: "invalid",
    cursor: "default",
    containment: "window",
    cursorAt: {
      left: -20,
      top: -20
    },
    helper: (evt) => {
      let source = $(evt.currentTarget);
      let attrs = source.get(0).__vue__.data;
      let proxy = source.clone();
      proxy.addClass("proxy");
      proxy.data("data", {
        state: "add",
        attributes: attrs
      });
      return proxy;
    },
    start: (evt) => {
      //拖动时取消右键、鼠标点击痕迹
      this.contextMenuClose();
      dsf.designer.broadcast(this, "clearContextMemuHandler", evt, null);
      dsf.designer.broadcast(this, "clearMouseDownHandler", evt, null);
    },
    stop: () => {
      this.dragEnd();
    }
  });
}

//初始化拖动
function initDrop() {
  $(".designer-page").droppable({
    tolerance: "pointer",
    // accept: function (a, b, c) {
    //   return false;
    // },
    drop: (evt, ui) => {
      let data = ui.helper.data("data");
      let proxy = $(this.dragProxy_el);
      let slotEl = proxy.closest("[slot-name]");
      let slotName = slotEl.attr("slot-name");
      if (slotName) {
        let container = dsf.designer.getParentControl(proxy);
        if (container) {
          this.dropHandler(evt, container, slotName, data);
        }
      }
      this.dragEnd();
    },
    over: () => {
      $(".designer-page").on("mousemove.tooldrag", (evt) => {
        this.dragMouseMoveHandler(evt);
        evt.preventDefault();
      });
    },
    out: () => {
      this.dragEnd();
    }
  });
}

//拖动到指定位置鼠标放开处理
function dropHandler(evt, container, slot, data) {
  let next = $(this.dragProxy_el).next(".designer-ctrl-proxy");
  if (this.dragProxy.show == true) {
    addToSlot.call(this, next.length ? next : null, container, slot, data);
  }
}
//拖动结束，无论成功失败
function dragEnd() {
  $(".designer-page").off("mousemove.tooldrag");
  $(this.dragProxy_el).appendTo(this.$el);
  this.dragProxy.show = false;
}

// 取出控件组数据 并去重
function checkData(data) {
  let arr = [];
  const getData = function (list) {
    list.forEach(function (row) {
      if (row.children) {
        getData(row.children);
      } else {
        arr.push(row);
      }
    });
  };
  getData(data);
  return _.uniqBy(arr, "dsfa_rm_id");
}

//将组件添加到插槽
async function addToSlot(obstacle, container, slot, data) {
  let component = container._isVue ? container : container.get(0).__vue__;
  let parentComponent = component,
    index = null,
    newComp = null;
  if (obstacle) {
    //如果拖放位置已经存在组件，则计算该组件位置，准备插入到其前
    let target = obstacle.get(0).__vue__;
    let slotItem = _.find(parentComponent.slots, (s) => s.name === slot);
    index = _.findIndex(slotItem.controls, (com) => com === target.__context__.props);
  }
  //从左侧工具箱拖放过来
  if (data.state == "add") {
    if (data.attributes.type == "control") {
      newComp = createComponent(data.attributes.data.code, {}, this.mobile);
      if (newComp.metadata) {
        newComp.metadata.level = 0;
      }
      newComp && this.addControl(parentComponent, slot, newComp, index);
    } else if (data.attributes.type == "metadata") {
      var metadata = data.attributes.data;
      var currentControl = null;
      if (metadata.currentControl) {
        currentControl = metadata.currentControl.value;
      } else {
        currentControl = metadata.controls[0].value;
      }
      if (currentControl.indexOf("dsf.") < 0) {
        currentControl = "dsf." + currentControl;
      }
      newComp = createComponent(currentControl, {}, this.mobile);
      // let metadata_instance = dsf.mix({}, metadata);
      newComp.metadata.id = metadata.id;
      newComp.metadata.at = metadata.at;
      newComp.metadata.code = metadata.code;
      newComp.metadata.name = metadata.name;
      newComp.metadata.defaultValue = metadata.defaultValue;
      newComp.metadata.controls = metadata.controls;
      newComp.metadata.dataSource = metadata.dataSource;
      newComp.metadata.type = metadata.type;
      newComp.metadata.order = metadata.order;
      newComp.metadata.isFromMetaData = true;
      newComp.metadata.isuser = false;
      newComp.metadata.currentControl = newComp.metadata.controls[0];
      newComp.metadata.level = 0;
      newComp.metadata.valueAttributes = dsf.mix(true, [], newComp.metadata.valueAttributes, metadata.valueAttributes);
      if (metadata.rmId) {
        newComp.metadata.rmId = metadata.rmId;
      }
      newComp.metadata.rm_type = data.attributes.group.type_value;
      newComp.caption = metadata.code;
      newComp.label = metadata.name;
      newComp && this.addControl(parentComponent, slot, newComp, index);
    } else if (data.attributes.type == "business") {
      var controlData = data.attributes.data;
      let layoutProps = JSON.parse(controlData.content);
      dsf.recursionComponentProps(layoutProps, (props) => {
        let ctrl = dsf.createComponent(props.ctrlType, props);
        dsf.mix(props, ctrl);
        props.designId = dsf.uuid();
        if (props.isFormControl) {
          props.isBusiness = true;
        }
      });
      this.addControl(parentComponent, slot, layoutProps, index);
    } else if (data.attributes.type == "onlineControl") {
      let ctrlOptions = await dsf.importOnLineComponent(data.attributes.data.namespace);
      Vue.component(ctrlOptions.name, ctrlOptions);
      newComp = createComponent(data.attributes.data.code, {}, this.mobile, true);
      if (newComp.metadata) {
        newComp.metadata.level = 0;
      }
      newComp && this.addControl(parentComponent, slot, newComp, index);
      dsf.array.ensure(
        this.online,
        {
          name: ctrlOptions.name,
          namespace: data.attributes.data.namespace
        },
        (v) => v.namespace == data.attributes.data.namespace
      );
    }
  } else {
    data.component.$remove();
    this.$nextTick(() => {
      if (data.attributes.metadata && data.attributes.metadata.isuser) {
        //组件被移动过后取消元数据中的isuser属性，让元数据重新计算
        delete data.attributes.metadata.isuser;
      }
      this.addControl(parentComponent, slot, data.attributes, index);
    });
  }
}
// {id: "test.wenjin.form.sub", type: "3"}

async function getLayout(business, module, pname, title, isMobile, isTpl) {
  // console.log("dsfsfdsf")
  let b = business || "";
  let m = module || "";
  let pn = pname || "";
  let htmlPath = dsf.designer.getLayoutPath(b, m, pn, isMobile, isTpl);
  return this.$http.get(htmlPath, null, { responseType: "text" });
}

async function getExtJs(b, m, pn, isMobile, isTpl) {
  let extPath = dsf.designer.getExtPath(b, m, pn, isMobile, isTpl);
  return this.$http.get(extPath, null, { responseType: "text" });
}

async function getPageDocument(b, m, pn, isMobile) {
  let docPath = dsf.designer.getPageDocumet(b, m, pn, isMobile);
  return this.$http.get(docPath, null, { responseType: "text" });
}

function preView() {
  try {
    let page = this.$refs.viewProxy.$$getComponent();
    dsf.designer.createViewMetaData(page, this.mobile);
    this.$nextTick(() => {
      let str = dsf.designer.getLayoutInfo(page, true);
      str = dsf.designer.controlToJson(str);
      let x = "/";
      let layoutStr = `<script type="text/template" id="layout">\n${str}\n<${x}script>`;
      dsf.storage.session.set("layoutContent", layoutStr);
      // window.localStorage.setItem("layoutContent", layoutStr);
      window.open("./designer.html#" + (this.mobile ? "/mobile" : "/pc") + "/preview");
    });
  } catch (ex) {
    let message = "出现了未知的错误";
    if (dsf.type(ex) == "string") {
      message = ex;
    } else {
      dsf.error(ex);
    }
    dsf.layer.pc.message(message, false);
  }
}

function createComponent(name, attrs, isMobile) {
  let fn = !isMobile ? dsf.designer.createComponent : dsf.designer.createMobileComponent;
  let _attrs = dsf.mix(attrs, { designProxy: true });
  return fn(name, _attrs);
}

//获取控件组
function requestControlGroupContent(params) {
  let ids = _.map(params, (el) => {
    return el.id;
  });
  return this.$webAPI.getComponents(ids).then(({ data }) => {
    return data;
  });
}
//获取元数据组
function requestMetaDataGroupContent(params) {
  let httpList = [];
  for (let i = 0; i < params.length; i++) {
    if (params[i].type == "3") {
      httpList.push(this.$http.get("/rm/list/meta", { namespace: params[i].id }));
    } else if (params[i].type == "8") {
      httpList.push(this.$http.get("/dbsource/rmId", { rmId: params[i].id }));
    }
  }
  return this.$http.all(httpList);
}

//获取业务组件中的组件信息
function requestBusinessComponent(params) {
  let ids = _.map(params, (el) => {
    return el.id;
  });
  return this.$webAPI.getBusinessComponets(ids);
}

//将元数据添加到设计器的元数据组中
function metadataToUseToolGroup(group) {
  let id = group.type_value == 8 ? group.dsfa_rm_id : group.ID;
  let result = {
    id: id,
    title: group.name,
    type: group.type_value,
    items: []
  };
  _.each(group.metadata, (m) => {
    let item = {
      type: "metadata",
      name: m.name,
      data: m,
      group: group,
      id: m.id
    };
    if (group.type_value == 8) {
      item.rmId = group.dsfa_rm_id;
    }
    result.items.push(item);
  });
  let index = _.findIndex(this.useMetaGroup.groups, (g) => {
    return g.id == result.id;
  });
  if (index < 0) {
    this.useMetaGroup.groups.push(result);
  } else {
    this.useMetaGroup.groups[index].items = result.items;
  }
  this.$nextTick(() => {
    //附加拖动
    this.initToolDrag();
  });
}

//将业务组件添加到设计器可选业务组件组中
function businessToUseToolGroup(group) {
  let result = {
    id: group.rmId,
    title: group.name,
    type: businessCode,
    items: []
  };
  _.each(group.bcList, (m) => {
    let item = {
      type: "business",
      name: m.name,
      data: m,
      group: group,
      id: m.id
    };
    result.items.push(item);
  });
  let index = _.findIndex(this.useBusinessControlGroup.groups, (g) => {
    return g.id == result.id;
  });
  if (index < 0) {
    this.useBusinessControlGroup.groups.push(result);
  } else {
    this.useBusinessControlGroup.groups[index].items = result.items;
  }
  this.$nextTick(() => {
    //附加拖动
    this.initToolDrag();
  });
}

function controlToUseToolGroup(group) {
  let result = {
    id: group.rmId,
    title: group.name,
    type: controlCode,
    items: []
  };
  for (let i = 0; i < group.controlList.length; i++) {
    let m = group.controlList[i];
    if (this.mobile && m.isMobile != 1) {
      continue;
    }
    let item = {
      type: "control",
      name: m.name,
      data: m,
      group: group,
      id: m.id
    };
    result.items.push(item);
  }
  let index = _.findIndex(this.useControlsGroup.groups, (g) => {
    return g.id == result.id;
  });
  if (index < 0) {
    this.useControlsGroup.groups.push(result);
  } else {
    this.useControlsGroup.groups[index].items = result.items;
  }
  // this.useControlsGroup.groups
  this.$nextTick(() => {
    //附加拖动
    this.initToolDrag();
  });
}
</script>
