<template>
  <el-container style="height: 100%">
    <el-aside style="width: 500px; overflow: auto">
      <el-container>
        <el-header style="padding: 0 0 10px 0; text-align: right; height: auto">
          <DsfButton size="small" icon="sousuofangda" @click="openDialog">添加自定义布局</DsfButton>
        </el-header>
        <el-main style="padding: 0; height: 100%">
          <el-table
            ref="table"
            :show-header="true"
            :data="tableData"
            max-height="350"
            size="small"
            highlight-current-row
            border
            @row-click="rowClickHandler">
            <el-table-column label="#" type="index" align="center"></el-table-column>
            <el-table-column label="名称" prop="name"></el-table-column>
            <el-table-column label="布局类型" prop="typeName" width="120px"></el-table-column>
            <el-table-column label="图标" prop="icon" width="70px" align="center">
              <template v-slot:default="{row}">
                <DsfButton btn-style="icon-text" size="small" :icon="getIcon(row.icon)"></DsfButton>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="100px" align="center">
              <template v-slot:default="{row, $index}">
                <DsfButton v-if="row.type != 'table'" btn-style="icon-text" icon="shanchu" size="small" title="删除" @click="removeData(row, $index)"></DsfButton>
                <DsfButton v-if="$index > 0" btn-style="icon-text" icon="shang" size="small" title="上移" @click="up(row, $index)"></DsfButton>
                <DsfButton v-if="$index < tableData.length - 1" btn-style="icon-text" icon="xia" size="small" title="下移" @click="down(row, $index)"></DsfButton>
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-aside>
    <el-main style="padding: 0">
      <el-container>
        <el-form v-if="formModel" :model="formModel" size="small" label-width="120px" style="width: 90%;">
          <el-form-item label="ID">
            <el-input v-model="formModel.id" disabled></el-input>
          </el-form-item>
          <el-form-item label="名称">
            <el-input v-model="formModel.name"></el-input>
          </el-form-item>
          <el-form-item label="图标">
            <el-input v-model="formModel.icon"></el-input>
          </el-form-item>
          <el-form-item label="布局类型">
            <el-input v-model="formModel.typeName" disabled></el-input>
          </el-form-item>
          <el-form-item v-if="formModel.type == 'table'" label="是否隐藏">
            <el-radio v-model="formModel.hide" :label="true">是</el-radio>
            <el-radio v-model="formModel.hide" :label="false">否</el-radio>
          </el-form-item>
          <el-form-item label="背景色">
            <el-input v-model="formModel.backgroundColor" :disabled="formModel.type == 'table'"></el-input>
          </el-form-item>
          <template v-if="layerConfig[formModel.type] && layerConfig[formModel.type].attr">
            <div style="color: #888; font-weight: bold;padding: 0 0 15px 30px;">
              <span style="border-left: 4px solid #ddd;padding-left:5px;">布局私有属性</span>
            </div>
            <template v-for="(item, k) in layerConfig[formModel.type].attr">
              <el-form-item :key="k">
                <template v-slot:label>
                  {{ item.label }}
                  <el-popover v-if="item.tips" :content="item.tips" placement="top" trigger="hover">
                    <i slot="reference" class="iconfont icon-yiwen"></i>
                  </el-popover>
                </template>
                <component :is="item.ctrl" v-model="formModel.attr[k]" v-bind.sync="item.attr" :placeholder="item.tips || ''"></component>
              </el-form-item>
            </template>
          </template>
        </el-form>
      </el-container>
    </el-main>
    <el-dialog title="选择布局" :visible.sync="dialogShow" width="400px" append-to-body>
      <el-radio-group v-model="radio">
        <el-radio v-for="item in layerConfig" :key="item.type" :label="item.type">{{ item.name }}</el-radio>
      </el-radio-group>
      <template v-slot:footer>
        <dsf-button @click="addData">确定</dsf-button>
        <dsf-button type="plain" @click="dialogShow = fasle">取消</dsf-button>
      </template>
    </el-dialog>
  </el-container>
</template>

<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";

/**
 *
 * @param val           值
 * @param defaultValue  默认值
 * @param unit          默认单位
 * @returns {*}
 */
function emptyFormat(val, defaultValue, unit = 'px') {
  if (!val) return defaultValue;
  val = val.trim();
  val = val.length ? val : defaultValue;
  if (unit && val.match(/^\d*.?\d+$/)) {
    val = val + unit;
  }
  return val;
}

export default {
  name: "DesDataGridLayoutSetting",
  mixins: [propertyDialogSetting],
  data() {
    return {
      layerConfig: layerConfig,
      dialogShow: false,
      formModel: null,
      radio: 'card',
      tableData: [
        {
          id: 'default',
          name: '表格',
          icon: 'icon-biaodanzujian-biaoge',
          type: 'table',
          typeName: '表格',
          hide: false,
          backgroundColor: '#ffffff'
        }
      ]
    }
  },
  created() {
    if (this.currentData) {
      this.tableData = this.currentData;
    }
  },
  mounted() {
    this.$refs.table.setCurrentRow(this.tableData[0]);
    this.formModel = this.tableData[0];
  },
  methods: {
    yes() {
      this.tableData.forEach(d => {
        if (d.type == 'card') {
          let { style } = this.getStyleByCard(d.attr);
          d.style = style;
        }
      });
      return this.tableData;
    },
    getStyleByCard({columns, marginBottom, marginLeft, maxWidth, minWidth}) {
      marginBottom = emptyFormat(marginBottom, '0px');
      marginLeft = emptyFormat(marginLeft, '0px');
      maxWidth = emptyFormat(maxWidth, '100%');
      minWidth = emptyFormat(minWidth, 'none');
      let flexBasis = maxWidth;
      if (columns > 0) {
        flexBasis = `calc((100% - ${marginLeft} * ${columns}) / ${columns})`
      }
      return {
        style: {
          'margin-bottom': marginBottom,
          'margin-left': marginLeft,
          'min-width': minWidth,
          'max-width': maxWidth,
          'flex-basis': flexBasis
        }
      }
    },
    getIcon(icon) {
      let temp = "icon-";
      if (icon.startsWith(temp)) {
        return icon.substr(temp.length);
      }
      return icon;
    },
    rowClickHandler(row) {
      this.formModel = row;
    },
    openDialog(){
      this.dialogShow = true;
    },
    addData() {
      let layerItem = this.layerConfig[this.radio];
      let data = {
        id: dsf.uuid(16),
        name: layerItem.name + this.tableData.length,
        icon: layerItem.icon,
        type: layerItem.type,
        typeName: layerItem.name,
        backgroundColor: '#ffffff',
        attr: Object.assign({}, layerItem.defaultArr)
      };
      this.tableData.push(data);
      this.dialogShow = false;
      this.$nextTick(() => {
        this.$refs.table.setCurrentRow(data);
        this.formModel = data;
        this.radio = 'card';
      });
    },
    removeData(row, index) {
      this.tableData.splice(index, 1);
    },
    up(row, index) {
      let targetButon = this.tableData[index - 1];
      this.$set(this.tableData, index - 1, row);
      this.$set(this.tableData, index, targetButon);
    },
    down(row, index) {
      let targetButon = this.tableData[index + 1];
      this.$set(this.tableData, index + 1, row);
      this.$set(this.tableData, index, targetButon);
    },
  }
}

const layerConfig = {
  card: {
    type: 'card',
    name: '布局',
    icon: 'icon-wnet',
    attr: {
      columns: {
        label: '一行几列',
        ctrl: 'el-input-number',
        tips: '0 表示自适应；大于0则在列宽允许的范围内有效。',
        attr: {
          step: 1,
          stepStrictly: true,
          min: 0
        }
      },
      marginBottom: {
        label: '行间距',
        tips: '默认0px，默认单位px',
        ctrl: 'el-input'
      },
      marginLeft: {
        label: '最小列间距',
        tips: '默认0px，默认单位px',
        ctrl: 'el-input'
      },
      maxWidth: {
        label: '列最大宽',
        tips: '默认100%，默认单位px',
        ctrl: 'el-input'
      },
      minWidth: {
        label: '列最小宽',
        tips: 'none 表示不设置最小宽，默认值none，默认单位px',
        ctrl: 'el-input'
      }
    },
    defaultArr: {
      columns: 0,
      marginBottom: '15px',
      marginLeft: '15px',
      maxWidth: '100%',
      minWidth: 'none'
    }
  }
};

</script>