<template>
  <el-container class="ue-header-set">
    <el-row class="content">
      <el-col :span="6" class="left">
        <el-checkbox v-model="checkAll" border :indeterminate="isIndeterminate" @change="chooseAll">全选</el-checkbox>
        <ul class="check-list">
          <li v-for="(item, it) in tools" :key="item.text">
            <el-checkbox :label="item.val" :value="item.checked" :checked="false" name="type" @change="chooseItem(item, it)">{{
              item.text
            }}</el-checkbox>
          </li>
        </ul>
      </el-col>
      <el-col :span="18" class="right">
        <div class="ue-container">
          <quill-editor
            v-if="showEditor"
            ref="myQuillEditor"
            :options="option"
            class="quill-editor"
            :style="{
              display: 'flex',
              height: '100%',
              'flex-direction': 'column',
            }"
          >
          </quill-editor>
        </div>
      </el-col>
    </el-row>
  </el-container>
</template>

<script>
import { quillEditor } from "vue-quill-editor";
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";

export default {
  name: "MobileUeditorToolbar",
  mixins: [propertyDialogSetting],
  components: {
    quillEditor,
  },
  data() {
    return {
      checkAll: false,
      isIndeterminate: false, //全选不确定状态
      tools: [
        { val: "bold", text: "加粗" },
        { val: "italic", text: "斜体" },
        { val: "underline", text: "下划线" },
        { val: "strike", text: "中划线" },
        { val: "blockquote", text: "引用" },
        { val: "code-block", text: "代码块" },
        { val: { header: 1 }, text: "一级标题" },
        { val: { header: 2 }, text: "二级标题" },
        { val: { list: "ordered" }, text: "有序列表" },
        { val: { list: "bullet" }, text: "无序列表" },
        { val: { script: "sub" }, text: "下标" },
        { val: { script: "super" }, text: "上标" },
        { val: { indent: "-1" }, text: "取消首行缩进" },
        { val: { indent: "+1" }, text: "首行缩进" },
        { val: { size: ["small", false, "large", "huge"] }, text: "字号" },
        { val: { header: [1, 2, 3, 4, 5, 6, false] }, text: "段落" },
        { val: { color: [] }, text: "字体颜色" },
        { val: { background: [] }, text: "背景色" },
        { val: { font: [] }, text: "字体" },
        { val: { align: [] }, text: "对齐方式" },
        { val: "clean", text: "清除样式" },
        { val: "link", text: "链接" },
        { val: "image", text: "上传图片" },
        { val: "video", text: "视频地址" },
      ],
      checkedTools: this.owner.toolBar,
      option: {
        modules: {
          toolbar: [],
        },
      },
      showEditor: true,
    };
  },
  watch: {
    checkedTools() {
      this.renderUEditor();
    },
  },
  created() {
    this.initData();
    this.judgeCheckAllStatus();
  },
  mounted() {
    this.renderUEditor();
  },
  methods: {
    initData() {
      this.tools.forEach((item) => {
        if (this.checkedTools.length === 0) {
          item.checked = true;
        } else {
          item.checked = false;
        }
      });
      for (let checked of this.checkedTools) {
        let index = this.tools.findIndex((item) => {
          if (typeof checked === "object") {
            return typeof item.val === "object" && JSON.stringify(item.val) === JSON.stringify(checked);
          }
          return item.val === checked;
        });
        if (index > -1) {
          this.tools[index].checked = true;
        }
      }
    },
    judgeCheckAllStatus() {
      let checkAll = !this.tools.find((item) => !item.checked);
      if (checkAll) {
        this.checkAll = true;
        this.isIndeterminate = false;
        return;
      }

      let checksome = this.tools.findIndex((item) => item.checked);
      if (checksome !== -1) {
        this.checkAll = false;
        this.isIndeterminate = true;
        return;
      }

      this.checkAll = false;
      this.isIndeterminate = false;
    },
    renderUEditor() {
      this.showEditor = false;
      this.option.modules.toolbar = this.checkedTools;
      this.$nextTick(() => {
        this.showEditor = true;
      });
    },
    chooseItem(item, index) {
      const current = _.assign({}, item, { checked: !item.checked });
      this.tools.splice(index, 1, current);
      this.refreshCheckedArr();
      this.judgeCheckAllStatus();
    },
    chooseAll() {
      this.isIndeterminate = false;
      if (this.checkAll) {
        this.tools.forEach((item) => {
          item.checked = true;
        });
      } else {
        this.tools.forEach((item) => {
          item.checked = false;
        });
      }
      this.refreshCheckedArr();
    },
    refreshCheckedArr() {
      const arr = [];
      for (let item of this.tools) {
        if (!item.checked) continue;
        arr.push(item.val);
      }
      this.checkedTools = arr;
    },
    yes() {
      return this.checkedTools;
    },
  },
};
</script>

<style lang="scss" scoped>
.ue-header-set {
  height: 100%;
  .content {
    width: 100%;
    .left {
      height: 100%;
      position: relative;
      .check-list {
        margin-top: 15px;
        height: calc(100% - 56px);
        width: 100%;
        overflow-y: scroll;
        box-sizing: border-box;
        padding-right: 5px;
        li {
          padding-top: 5px;
          width: 100%;
        }
        li + li {
          margin-top: 5px;
          border-top: 1px solid #eeeeee;
        }
      }
    }
    .right {
      height: 100%;
      .ue-container {
        width: 98%;
        height: 100%;
        padding-left: 2%;
        box-sizing: border-box;
        .quill-editor {
          display: "flex";
          height: "100%";
          flex-direction: "column";
        }
      }
    }
  }
}
</style>
