import { Mark } from "tiptap/dist/tiptap.common"
import { updateMark, removeMark } from  "tiptap-commands/dist/commands.common"

export default class BackgroundMark extends Mark {

  get name() {
    return 'backgroundmark'
  }

  get defaultOptions() {
    return {
      color: null,
    }
  }

  get schema() {
    return {
      attrs: {
        color: {
            default: null,
        },
      },
      inclusive: false,
      parseDOM: [
        {
            style: 'background',
            getAttrs: value => {
                return { color: value };
            },
        },       
      ],
      toDOM: (node) => {
          return ['span', {
            style: `background: ${node.attrs.color};`,
          }, 0]
      },
    }
  }

  commands({ type }) {
    return attrs => {
      if (attrs.color) {
        return updateMark(type, attrs)
      }
      return removeMark(type)
    }
  }

}
