<template>
  <el-container>
    <el-header style="padding: 10px 0px; text-align: right">
      <DsfButton @click="add">角色选择</DsfButton>
    </el-header>
    <el-main style="padding: 0px">
      <el-table ref="table" class="ds-table" :border="true" :data="tableData">
        <el-table-column
          label="序号"
          type="index"
          width="50"
          :header-align="'center'"
          :align="'center'"
        >
        </el-table-column>
        <el-table-column
          label="角色名称"
          prop="name"
          :header-align="'center'"
          :align="'center'"
        >
        </el-table-column>
        <el-table-column
          label="角色id"
          prop="roleid"
          width="280"
          :header-align="'center'"
          :align="'center'"
        >
        </el-table-column>

        <el-table-column
          label="操作"
          :header-align="'center'"
          :align="'center'"
          :width="120"
        >
          <template v-slot:default="scope">
            <DsfButton
              :size="'small'"
              title="删除"
              icon="guanbi2"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px"
              @click="deleted(scope)"
            ></DsfButton>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/mixins/propertyDialogSetting";
export default {
  name: "AccessPrivilege",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
    };
  },
  created() {
    this.tableData = this.currentData || [];
  },
  methods: {
    add() {
      let _this = this;
      this.$openDialog({
        title: "角色选择",
        width: this.config.width,
        height: this.config.height,
        top: "10vh",
        params: {
          path: "dsfa/oua/role/selectrole",
        },
        btns: [
          {
            text: "确定",
            handler(res) {
              if (res.content) {
                let vm = res.content;
                if (vm && vm.yes && dsf.isFunction(vm.yes)) {
                  let returnValue = vm.yes();
                  if (!returnValue) {
                    return false;
                  } else {
                    _.forEach(returnValue, (d) => {
                      let result = _this.tableData.filter(function (v, i) {
                        return v["roleid"] == d["_id"];
                      });
                      if (result.length > 0) {
                        result.name = d["_name"];
                      } else {
                        _this.tableData.push({
                          roleid: d["_id"],
                          name: d["_name"],
                        });
                      }
                    });
                  }
                }
              }
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
    deleted(scope) {
      dsf.array.remove(this.tableData, scope.row);
    },
    yes() {
      return this.tableData;
    },
  },
};
</script>
