import layoutPreView from "_platform/output/layoutPreView";
import layoutMobilePreView from "_platform/output/layoutMobilePreView";
// import test from "_platform/designer/page/dsfDesigner/test";
import seatDesign from "_platform/designer/seat/seatDesign";
import seatView from "_platform/designer/seat/seatView";
import exam from "_platform/designer/page/dsfDesigner/examPage";
import MobileSetting from '_platform/designer/page/dsfDesigner/mobileSetting';
import DsfUserComponentDev from '../page/dsfDesigner/dsfUserComponentDev.vue'
//此处导出设计器使用的默认路由

let router = [
  //设计预览路由
  {
    path: "/pc/preview",
    name: "DsfLayoutPreView",
    meta: {
      title: "预览"
    },
    component: layoutPreView,
    props(router) {
      return {
        mobile: router.query['mobile'] == '1' ? true : false,
        tpl: router.query['tpl'] == '1' ? true : false
      }
    }
  }, {
    path: "/mobile/preview",
    name: "DsfMobileLayoutPreView",
    meta: {
      title: "预览"
    },
    component: layoutMobilePreView
  }, {
    path: "/componentDev",
    name: "componentDev",
    meta: {
      title: "组件开发"
    },
    component: DsfUserComponentDev,
    props(router) {
      return {
        mobile: router.query['mobile'] == '1' ? true : false,
        tpl: router.query['tpl'] == '1' ? true : false
      }
    }
  }, {
    path: "/seatDesign",
    name: "seatDesign",
    component: seatDesign
  }, {
    path: "/seatView",
    name: "seatView",
    component: seatView
  },
  {
    path: "/dormitory",
    name: "dormitory",
    component: () => import( /* webpackChunkName: "Dormitory" */ "_platform/designer/dormitory/dormitory.vue")
  },
  {
    path: "/flow",
    name: "Flow",
    meta: {
      title: "流程设计器"
    },
    component: () => import( /* webpackChunkName: "Flow" */ "_platform/designer/flow/flow.vue")
  },
  {
    path: "/todoTemp",
    name: "TodoTemp",
    meta: {
      title: "待办事项"
    },
    component: () => import( /* webpackChunkName: "TodoTemp" */ "_platform/pc/bhc/flow/todoTemp.vue")
  },
  {
    path: "/mobilesetting",
    name: "MobileSetting",
    meta: {
      title: "移动端设置"
    },
    component: MobileSetting
  },
  {
    path: "/exam",
    name: "Exam",
    component: exam,
    meta: {
      title: "在线考试"
    },
  },
];

export default router;